export let country_descriptions = {
  Afghanistan:
    "Since the late 1970s, Afghanistan's history has been dominated by extensive warfare, including coups, invasions, insurgencies, and civil wars. The conflict began in 1978 when a communist revolution established a socialist state (itself a response to the dictatorship established following a coup d'\u00e9tat in 1973), and subsequent infighting prompted the Soviet Union to invade Afghanistan in 1979. Mujahideen fought against the Soviets in the Soviet\u2013Afghan War and continued fighting among themselves following the Soviets' withdrawal in 1989. The Taliban controlled most of the country by 1996, but their Islamic Emirate of Afghanistan received little international recognition before its overthrow in the 2001 US invasion of Afghanistan. The Taliban returned to power in 2021 after capturing Kabul, ending the 2001\u20132021 war.[35] The Taliban government remains internationally unrecognized.[36]\n",
  "\u00c5land Islands":
    "\u00c5land's autonomous status means that those provincial powers normally exercised by representatives of the central Finnish Government are largely exercised by its own government. The current demilitarised, neutral position of \u00c5land dates back to the Paris Peace Treaty after the \u00c5land War in the 1850s.[12]\n",
  Albania:
    "Since its independence in 1912, Albania has undergone a diverse political evolution, transitioning from a monarchy to a communist regime before becoming a sovereign parliamentary constitutional republic. Governed by a constitution prioritizing the separation of powers, the country's political structure includes a parliament, a ceremonial president, a functional prime minister and a hierarchy of courts. Albania is a developing country with an upper-middle income economy driven by the service sector, with manufacturing and tourism also playing significant roles.[9] After the dissolution of its communist system the country shifted from centralized planning to an open market economy.[10] Albanian citizens have universal health care access and free primary and secondary education.\n",
  Algeria:
    "Spanning 2,381,741 square kilometres (919,595\u00a0sq\u00a0mi), Algeria is the world's tenth-largest nation by area, and the largest nation in Africa.[14] It has a semi-arid climate, with the Sahara desert dominating most of the territory except for its fertile and mountainous north, where most of the population is concentrated.  With a population of 44 million, Algeria is the tenth-most populous country in Africa, and the 33rd-most populous country in the world.  Algeria's official languages are Arabic and Tamazight; French is used in media, education, and certain administrative matters. The vast majority of the population speak the Algerian dialect of Arabic. Most Algerians are Arabs, with Berbers forming a sizeable minority. Sunni Islam is the official religion and practised by 99 percent of the population.[15]\n",
  "American Samoa":
    'Inhabited by Polynesians since prehistory, American Samoa was first contacted by Europeans in the 18th century. The islands attracted missionaries, explorers, and mariners, particularly to the highly protected natural harbor of Pago Pago. The United States took possession of American Samoa in the late 19th century, developing it into a major naval outpost; the territory\'s strategic value was reinforced by the Second World War and subsequent Cold War. In 1967, American Samoa became self-governing with the adoption of a constitution; its local government is republican in form, with separate executive, legislative, and judicial branches. It remains officially unorganized and is thus directly administered by the federal government. American Samoa is listed among seventeen "non-self-governing territories" but is a member of several intergovernmental organizations, including the Pacific Community, Pacific Islands Forum (PIF), Alliance of Small Island States (AOSIS), and International Olympic Committee (IOC).[8]\n',
  Andorra:
    "Andorra is the sixth-smallest state in Europe, with an area of 468 square kilometres (181\u00a0sq\u00a0mi) and a population of approximately 79,034.[15][16] The Andorran people are a Romance ethnic group closely related to Catalans.[17] Andorra is the world's 16th-smallest country by land and 11th-smallest by population.[18] Its capital, Andorra la Vella, is the highest capital city in Europe, at an elevation of 1,023 metres (3,356 feet) above sea level.[19] The official language is Catalan, but Spanish, Portuguese, and French are also commonly spoken.[3][20]\n",
  Angola:
    "The banning of the slave trade in the 19th century severely disrupted Kongo's undiversified economic system. European settlers gradually began to establish themselves in the interior. The Portuguese colony that became Angola did not achieve its present borders until the early 20th century. There had been strong resistance by native groups such as the Cuamato, the Kwanyama, and the Mbunda.\n",
  Anguilla:
    'In reference to the island\'s shape, the Italian anguilla, meaning "eel"  (in turn, from the Latin diminutive of anguis, "snake") was used as its name.[8][10][11][12] Anguillan tradition holds that Christopher Columbus named the island.[13]\n',
  Antarctica:
    "The ice shelves of Antarctica were probably first seen in 1820, during a Russian expedition led by Fabian Gottlieb von Bellingshausen and Mikhail Lazarev. The decades that followed saw further exploration by French, American, and British expeditions. The first confirmed landing was by a Norwegian team in 1895. In the early 20th century, there were a few expeditions into the interior of the continent. British explorers Robert Falcon Scott and Ernest Shackleton were the first to reach the magnetic South Pole in 1909, and the geographic South Pole was first reached in 1911 by Norwegian explorer Roald Amundsen.[4]\n",
  "Antigua & Barbuda":
    "In 1493, Christopher Columbus surveyed the island of Antigua, which he named for the Church of Santa Mar\u00eda La Antigua.[14] Great Britain colonized Antigua in 1632 and Barbuda in 1678.[14] A part of the Federal Colony of the Leeward Islands from 1871, Antigua and Barbuda joined the West Indies Federation in 1958.[15] With the breakup of the federation in 1962, it became one of the West Indies Associated States in 1967.[16] Following a period of internal self-governance, it gained full independence from the United Kingdom on 1 November 1981. Antigua and Barbuda is a member of the Commonwealth and a Commonwealth realm; it is a constitutional monarchy with Charles III as its head of state.[17]\n",
  Argentina:
    "Following the death of President Juan Per\u00f3n in 1974, his widow and vice president, Isabel Per\u00f3n, ascended to the presidency, before being overthrown in 1976. The following military junta, which was supported by the United States, persecuted and murdered thousands of political critics, activists, and leftists in the Dirty War, a period of state terrorism and civil unrest that lasted until the election of Ra\u00fal Alfons\u00edn as president in 1983.\n",
  Armenia:
    "Between the 16th and 19th centuries, the traditional Armenian homeland composed of Eastern Armenia and Western Armenia came under the rule of the Ottoman and Persian empires, repeatedly ruled by either of the two over the centuries. By the 19th century, Eastern Armenia had been conquered by the Russian Empire, while most of the western parts of the traditional Armenian homeland remained under Ottoman rule. During World War I, up to 1.5 million Armenians living in their ancestral lands in the Ottoman Empire were systematically exterminated in the Armenian genocide. In 1918, following the Russian Revolution, all non-Russian countries declared their independence after the Russian Empire ceased to exist, leading to the establishment of the First Republic of Armenia. By 1920, the state was incorporated into the Soviet Union as the  Armenian SSR. The modern Republic of Armenia became independent in 1991 during the dissolution of the Soviet Union.\n",
  Aruba:
    "In 1986, it became a constituent country within the Kingdom of the Netherlands, and acquired the formal name the Country of Aruba.\n",
  Australia:
    "Australia is a federal parliamentary democracy and constitutional monarchy. Its population of over 27 million is highly urbanised and heavily concentrated on the eastern seaboard.[11][30] Canberra is the nation's capital, while its most populous cities are Sydney and Melbourne, both with a population of over 5 million.[31] Australia's culture is diverse, and the country has one of the highest foreign-born populations in the world.[32][33] It has a highly developed market economy and one of the highest per capita incomes globally.[34][35][36] Its abundant natural resources and well-developed international trade relations are crucial to the country's economy. It ranks highly for quality of life, health, education, economic freedom, civil liberties and political rights.[37]\n",
  Austria:
    "The area of today's Austria has been inhabited since at least the Paleolithic period. Around 400 BC, it was inhabited by the Celts and then annexed by the Romans in the late 1st century BC. Christianization in the region began in the 4th and 5th centuries, during the late Roman period, followed by the arrival of numerous Germanic tribes during the Migration Period.[15] Austria, as a unified state, emerged from the remnants of the Eastern and Hungarian March at the end of the first millennium, first as a frontier march of the Holy Roman Empire, it then developed into a duchy in 1156, and was made an Archduchy in 1453. Being the heartland of the Habsburg monarchy since the late 13th century, Austria was a major imperial power in Central Europe for centuries and from the 16th century, Vienna was also serving as the Holy Roman Empire's administrative capital.[16] Before the dissolution of the empire two years later, in 1804, Austria established its own empire, which became a great power and one of the largest states in Europe. The empire's defeat in wars and the loss of territories in the 1860s paved the way for the establishment of Austria-Hungary in 1867.[17]\n",
  Azerbaijan:
    "By the late 19th century, an Azerbaijani national identity emerged when the Azerbaijan Democratic Republic proclaimed its independence from the Transcaucasian Democratic Federative Republic in 1918, a year after the Russian Empire collapsed, and became the first secular democratic Muslim-majority state. In 1920, the country was conquered and incorporated into the Soviet Union as the Azerbaijan SSR.[12][14] The modern Republic of Azerbaijan proclaimed its independence on 30 August 1991,[15][16] shortly before the dissolution of the Soviet Union. In September 1991, the ethnic Armenian majority of the Nagorno-Karabakh region formed the self-proclaimed Republic of Artsakh,[17] which became de facto independent with the end of the First Nagorno-Karabakh War in 1994, although the region and seven surrounding districts remained internationally recognized as part of Azerbaijan.[18][19][20][21] Following the Second Nagorno-Karabakh War in 2020, the seven districts and parts of Nagorno-Karabakh were returned to Azerbaijani control.[22] An Azerbaijani offensive in 2023 ended the Republic of Artsakh and resulted in the flight of Nagorno-Karabakh Armenians.[23]\n",
  Bahamas:
    "The Bahamas became a British crown colony in 1718, when the British clamped down on piracy. After the American Revolutionary War, the Crown resettled thousands of American Loyalists to The Bahamas; they took enslaved people with them and established plantations on land grants. Enslaved Africans and their descendants constituted the majority of the population from this period on. The slave trade was abolished by the British in 1807. Although slavery in The Bahamas was not abolished until 1834, The Bahamas became a haven of manumission for African slaves, from outside the British West Indies, in 1818.[16] Africans liberated from illegal slave ships were resettled on the islands by the Royal Navy, while some North American slaves and Seminoles escaped to The Bahamas from Florida. Bahamians were even known to recognise the freedom of enslaved people carried by the ships of other nations which reached The Bahamas. Today Black-Bahamians make up 90% of the population of 400,516.[14]\n",
  Bahrain:
    "In the late 1800s, following successive treaties with the British, Bahrain became a protectorate of the United Kingdom.[17] In 1971, it declared independence. Formerly an emirate, Bahrain was declared a semi-constitutional monarchy in 2002, and Article 2 of the newly adopted constitution made Sharia a principal source for legislation.\n",
  Bangladesh:
    "In 1947, East Bengal became the most populous province in the Dominion of Pakistan and was renamed East Pakistan, with Dhaka as the legislative capital. The Bengali Language Movement in 1952, the 1958 Pakistani coup d'\u00e9tat, and the 1970 Pakistani general election spurred Bengali nationalism and pro-democracy movements. The refusal of the Pakistani military junta to transfer power to the Awami League, led by Sheikh Mujibur Rahman, triggered the Bangladesh Liberation War in 1971. The Mukti Bahini, aided by India, waged a successful armed revolution; the conflict saw the Bangladeshi genocide. The new state of Bangladesh became a constitutionally secular state in 1972, although Islam was declared the state religion in 1988.[19] In 2010, the Bangladesh Supreme Court reaffirmed secular principles in the constitution.[20] The Constitution of Bangladesh officially declares it a socialist state.[21]\n",
  Barbados:
    "On 30 November 1966, Barbados moved towards political independence and assumed the status of a Commonwealth realm, becoming a separate jurisdiction with Elizabeth II as the Queen of Barbados. On 30 November 2021, Barbados transitioned to a republic within the Commonwealth, replacing its monarchy with a ceremonial president.[9][10]\n",
  Belarus:
    "Between the medieval period and the 20th century, different states at various times controlled the lands of modern-day Belarus, including Kievan Rus', the Principality of Polotsk, the Grand Duchy of Lithuania, the Polish\u2013Lithuanian Commonwealth, and the Russian Empire. In the aftermath of the Russian Revolution in 1917, different states arose competing for legitimacy amid the Civil War, ultimately ending in the rise of the Byelorussian SSR, which became a founding constituent republic of the Soviet Union in 1922. After the Polish-Soviet War (1918\u20131921), Belarus lost almost half of its territory to Poland. Much of the borders of Belarus took their modern shape in 1939, when some lands of the Second Polish Republic were reintegrated into it after the Soviet invasion of Poland, and were finalized after World War\u00a0II. During World War II, military operations devastated Belarus, which lost about a quarter of its population and half of its economic resources. In 1945, the Byelorussian SSR became a founding member of the United Nations, along with the Soviet Union. The republic was home to a widespread and diverse anti-Nazi insurgent movement which dominated politics until well into the 1970s, overseeing Belarus' transformation from an agrarian to industrial economy.\n",
  Belgium:
    "Belgium is a sovereign state and a federal constitutional monarchy with a parliamentary system. Its institutional organization is complex and is structured on both regional and linguistic grounds. It is divided into three highly autonomous regions:[13] the Flemish Region (Flanders) in the north, the Walloon Region (Wallonia) in the south, and the Brussels-Capital Region in the middle.[14] Brussels is the smallest and most densely populated region, as well as the richest region in terms of GDP per capita. Belgium is also home to two main linguistic communities: the Flemish Community (Dutch-speaking), which constitutes about 60 percent of the population, and the French Community (French-speaking),[d] which constitutes about 40 percent of the population. A small German-speaking Community, making up around one percent of the population, exists in the East Cantons. The Brussels-Capital Region is officially bilingual in French and Dutch,[16] although French is the majority language and lingua franca.[17] Belgium's linguistic diversity and related political conflicts are reflected in its complex system of governance, made up of six different governments.\n",
  Belize:
    "Belize's abundance of terrestrial and marine plants and animals and its diversity of ecosystems, including extensive coral reefs, give it a key place in the globally significant Mesoamerican Biological Corridor.[15] It is considered a Central American and Caribbean nation with strong ties to both the American and Caribbean regions.[16]\n",
  Benin:
    "The official language of Benin is French, with indigenous languages such as Fon, Bariba, Yoruba and Dendi also spoken. The largest religious group in Benin is Christianity (52.2%), followed by Islam (24.6%) and African Traditional Religions (17.9%).[3] Benin is a member of the United Nations, the African Union, the Economic Community of West African States, the Organisation of Islamic Cooperation, the South Atlantic Peace and Cooperation Zone, Francophonie, the Community of Sahel\u2013Saharan States, the African Petroleum Producers Association and the Niger Basin Authority.\n",
  Bermuda:
    "Bermuda is named after Spanish explorer Juan de Berm\u00fadez, who discovered the archipelago in 1505. The islands have been permanently inhabited since 1612 when an English settlement was established at St. George's. Forming part of British America, Bermuda was governed under Royal charter by the Somers Isles Company until 1684, when it became a crown colony. The first enslaved Africans were taken to Bermuda in 1616. The Somers Isles Company ensured a steady flow of free but indentured servants until 1684, and most tobacco farms owned by overseas adventurers were sold to the tenants or other occupants after Bermuda-grown tobacco became steadily less profitable following the 1620s, becoming family farms that switched from growing tobacco for export to producing food (initially for local consumption).[6] Consequently, a plantation economy did not develop and the slave trade largely ceased by the end of the 17th century.[7] The economy instead became maritime-focused, with the colony serving as a base for merchants, privateers and the Royal Navy, giving its name to the Bermuda rig and Bermuda sloop. It became an imperial fortress, the most important British naval and military base in the western hemisphere with vast funds lavished on its Royal Naval Dockyard and military defences until the 1950s. Tourism has been a significant contributor to Bermuda's economy since the 19th century and after World War II, the territory became a prominent offshore financial centre and tax haven.\n",
  Bhutan:
    "Bhutan and neighbouring Tibet experienced the spread of Buddhism, which originated in the Indian subcontinent during the lifetime of Gautama Buddha. In the first millennium, the Vajrayana school of Buddhism spread to Bhutan from the southern Pala Empire of Bengal. During the 16th century, Ngawang Namgyal unified the valleys of Bhutan into a single state. Namgyal defeated three Tibetan invasions, subjugated rival religious schools, codified the Tsa Yig legal system, and established a government of theocratic and civil administrators. Namgyal became the first Zhabdrung Rinpoche and his successors acted as the spiritual leaders of Bhutan, like the Dalai Lama in Tibet. During the 17th century, Bhutan controlled large parts of northeast India, Sikkim and Nepal; it also wielded significant influence in Cooch Behar State.[18] Bhutan ceded the Bengal Duars to British India during the Bhutan War in the 19th century. The House of Wangchuck emerged as the monarchy and pursued closer ties with Britain in the subcontinent. In 1910, a treaty guaranteed British advice in foreign policy in exchange for internal autonomy in Bhutan. The arrangement continued under a new treaty with India in 1949 (signed at Darjeeling) in which both countries recognised each other's sovereignty. Bhutan joined the United Nations in 1971. It has since expanded relations with 55 countries. While dependent on the Indian military, Bhutan maintains its own military units.\n",
  Bolivia:
    "The sovereign state of Bolivia is a constitutionally unitary state divided into nine departments. Its geography varies as the elevation fluctuates, from the western snow-capped peaks of the Andes to the eastern lowlands, situated within the Amazon basin. One-third of the country is within the Andean mountain range. With an area of 1,098,581\u00a0km2 (424,164\u00a0sq\u00a0mi), Bolivia is the fifth-largest country in South America after Brazil, Argentina, Peru and Colombia, and, alongside Paraguay, is one of two landlocked countries in the Americas. It is the 27th largest country in the world, the largest landlocked country in the Southern Hemisphere, and the seventh largest landlocked country on earth, after Kazakhstan, Mongolia, Chad, Niger, Mali, and Ethiopia.\n",
  "Bosnia & Herzegovina":
    "The area has been inhabited since at least the Upper Paleolithic, but evidence suggests that during the Neolithic age, permanent human settlements were established, including those that belonged to the Butmir, Kakanj, and Vu\u010dedol cultures. After the arrival of the first Indo-Europeans, the area was populated by several Illyrian and Celtic civilizations. The ancestors of the South Slavic peoples that populate the area today arrived during the 6th through the 9th century. In the 12th century, the Banate of Bosnia was established; by the 14th century, this had evolved into the Kingdom of Bosnia. In the mid-15th century, it was annexed into the Ottoman Empire, under whose rule it remained until the late 19th century; the Ottomans brought Islam to the region. From the late 19th century until World War I, the country was annexed into the Austro-Hungarian monarchy. In the interwar period, Bosnia and Herzegovina was part of the Kingdom of Yugoslavia.  After World War II, it was granted full republic status in the newly formed Socialist Federal Republic of Yugoslavia. In 1992, following the breakup of Yugoslavia, the republic proclaimed independence. This was followed by the Bosnian War, which lasted until late 1995 and ended with the signing of the Dayton Agreement.\n",
  Botswana:
    "The economy is dominated by mining and tourism. Botswana has a per capita GDP (purchasing power parity) of about $20,158 as of 2024[update]. Botswana is the world's biggest diamond-producing country. Its relatively high gross national income per capita (by some estimates the fourth-largest in Africa) gives the country a relatively high standard of living and the second-highest Human Development Index of continental Sub-Saharan Africa (after South Africa). Botswana is a member of the Southern African Customs Union, the Southern African Development Community, the Commonwealth of Nations and the United Nations.\n",
  "Bouvet Island":
    "The island was first spotted on 1\u00a0January 1739 by the Frenchman Jean-Baptiste Charles Bouvet de Lozier, during a French exploration mission in the South Atlantic with the ships Aigle and Marie. They did not make landfall. He mislabeled the coordinates for the island, and it was not sighted again until 1808, when the British whaler James Lindsay encountered it and named it Lindsay Island.[5] The first claim to have landed on the island was made by the American sailor Benjamin Morrell, although this claim is disputed. In 1825, the island was claimed for the British Crown by George Norris, who named it Liverpool Island. He also reported having sighted another island nearby, which he named Thompson Island, but this was later shown to be a phantom island.\n",
  Brazil:
    "The territory of present-day Brazil was inhabited by numerous tribal nations prior to the landing of explorer Pedro \u00c1lvares Cabral in 1500. Subsequently claimed by the Portuguese Empire, Brazil remained a Portuguese colony until 1808, when the capital of the empire was transferred from Lisbon to Rio de Janeiro. In 1815, the colony was elevated to the rank of kingdom upon the formation of the United Kingdom of Portugal, Brazil and the Algarves. Independence was achieved in 1822 with the creation of the Empire of Brazil, a unitary state governed under a constitutional monarchy and a parliamentary system. The ratification of the first constitution in 1824 led to the formation of a bicameral legislature, now called the National Congress. Slavery was abolished in 1888. The country became a presidential republic in 1889 following a military coup d'\u00e9tat. An authoritarian military dictatorship emerged in 1964 and ruled until 1985, after which civilian governance resumed. Brazil's current constitution, formulated in 1988, defines it as a democratic federal republic.[16] Due to its rich culture and history, the country ranks thirteenth in the world by number of UNESCO World Heritage Sites.[17]\n",
  "British Indian Ocean Territory":
    "Since the 1980s, the Government of Mauritius has sought to regain control over the Chagos Archipelago, which was separated from the then Crown Colony of Mauritius by the UK in 1965 to form the British Indian Ocean Territory.  A February 2019 advisory opinion of the International Court of Justice called for the islands to be given to Mauritius.  Since this, the United Nations General Assembly and the International Tribunal for the Law of the Sea have reached similar decisions. On 3 November 2022, it was announced that the UK and Mauritius had decided to begin negotiations on sovereignty over the British Indian Ocean Territory, taking into account the international legal proceedings.[11] However, in December 2023, it was reported that the UK government was planning to discontinue the talks.[12]\n",
  "British Virgin Islands":
    "British Virgin Islanders are British Overseas Territories citizens and since 2002, are also British citizens.\n",
  Brunei:
    "At the Bruneian Empire's peak during the reign of Sultan Bolkiah (1485\u20131528), the state is claimed to have had control over the most of Borneo, including modern-day Sarawak and Sabah, as well as the Sulu archipelago and the islands off the northwestern tip of Borneo. There are also claims to its historical control over Seludong, the site of the modern Philippine capital of Manila, but Southeast Asian scholars believe the name of the location in question is actually in reference to Mount Selurong, in Indonesia.[18] The maritime state of Brunei was visited by the surviving crew of the  Magellan Expedition in 1521, and in 1578 it fought against Spain in the Castilian War.\n",
  Bulgaria:
    "One of the earliest societies in the lands of modern-day Bulgaria was the Karanovo culture (6,500 BC). In the 6th to 3rd century BC, the region was a battleground for ancient Thracians, Persians, Celts and Macedonians; stability came when the Roman Empire conquered the region in AD 45. After the Roman state splintered, tribal invasions in the region resumed. Around the 6th century, these territories were settled by the early Slavs. The Bulgars, led by Asparuh, attacked from the lands of Old Great Bulgaria and permanently invaded the Balkans in the late 7th century. They established the First Bulgarian Empire, victoriously recognised by treaty in 681 AD by the Byzantine Empire. It dominated most of the Balkans and significantly influenced Slavic cultures by developing the Cyrillic script. The First Bulgarian Empire lasted until the early 11th century, when Byzantine emperor Basil II conquered and dismantled it. A successful Bulgarian revolt in 1185 established a Second Bulgarian Empire, which reached its apex under Ivan Asen II (1218\u20131241). After numerous exhausting wars and feudal strife, the empire disintegrated and in 1396 fell under Ottoman rule for nearly five centuries.\n",
  "Burkina Faso":
    "Thomas Sankara came to power following a successful coup in 1983. As president, Sankara embarked on a series of ambitious socioeconomic reforms which included a nationwide literacy campaign, land redistribution to peasants, vaccinations for over 2 million children, railway and road construction, equalized access to education, and the outlawing of female genital mutilation, forced marriages, and polygamy. He served as the country's president until 1987 when he was deposed and assassinated in a coup led by Blaise Compaor\u00e9, who became president and ruled the country until his removal on 31 October 2014.\n",
  Burundi:
    "Burundi remains primarily a rural society, with just 13.4% of the population living in urban areas in 2019.[16] Burundi is densely populated, and many young people emigrate in search of opportunities elsewhere. Roughly 85% of the population are of Hutu ethnic origin, 15% are Tutsi, and fewer than 1% are Twa.[17] The official languages of Burundi are Kirundi, French, and English\u2014Kirundi being officially recognised as the sole national language.[18] English was made an official language in 2014.[19]\n",
  Cambodia:
    "After the Japanese occupation of Cambodia during the Second World War, it gained independence in 1953. The Vietnam War extended into the country in 1965 via the Ho Chi Minh and Sihanouk trails. A 1970 coup installed the US-aligned Khmer Republic, which was overthrown by the Khmer Rouge in 1975. The Khmer Rouge ruled the country and carried out the Cambodian genocide from 1975 until 1979, when they were ousted in the Cambodian\u2013Vietnamese War. The Vietnamese-occupied People's Republic of Kampuchea became the de facto government.\n",
  Cameroon:
    "Cameroon became a German colony in 1884 known as Kamerun. After World War I, it was divided between France and the United Kingdom as League of Nations mandates. France took 4/5 and the United Kingdom 1/5 of the territory and both ruled it under mandate until independence in 1960 and 1961 respectively.[11] The Union des Populations du Cameroun (UPC) political party advocated independence but was outlawed by France in the 1950s, leading to the national liberation insurgency fought between French and UPC militant forces until early 1971. In 1960, the French-administered part of Cameroon became independent, as the Republic of Cameroun, under President Ahmadou Ahidjo. The southern part of British Cameroons federated with it in 1961 to form the Federal Republic of Cameroon. The federation was abandoned in 1972. The country was renamed the United Republic of Cameroon in 1972 and back to the Republic of Cameroon in 1984 by a presidential decree by president Paul Biya. Biya, the incumbent president, has led the country since 1982 following Ahidjo's resignation; he previously held office as prime minister from 1975 onward. Cameroon is governed as a unitary presidential republic.\n",
  Canada:
    "Canada is a parliamentary democracy and a constitutional monarchy in the Westminster tradition. The country's head of government is the prime minister, who holds office by virtue of their ability to command the confidence of the elected House of Commons and is appointed by the governor general, representing the monarch of Canada, the ceremonial head of state. The country is a Commonwealth realm and is officially bilingual (English and French) in the federal jurisdiction. It is very highly ranked in international measurements of government transparency, quality of life, economic competitiveness, innovation, education and gender equality. It is one of the world's most ethnically diverse and multicultural nations, the product of large-scale immigration. Canada's long and complex relationship with the United States has had a significant impact on its history, economy, and culture.\n",
  "Cape Verde":
    "Since the early 1990s, Cape Verde has been a stable representative democracy and has remained one of the most developed and democratic countries in Africa. Lacking natural resources, its developing economy is mostly service-oriented, with a growing focus on tourism and foreign investment. Its population of around 596,000 (as of 2022) is of mostly African and a minor European heritage, and predominantly Roman Catholic, reflecting the legacy of Portuguese rule. A sizeable Cape Verdean diaspora community exists across the world, especially in the United States and Portugal, considerably outnumbering the inhabitants on the islands. Cape Verde is a member state of the African Union.\n",
  "Caribbean Netherlands":
    "The three islands gained their current status following the dissolution of the Netherlands Antilles on 10 October 2010.[12] At the same time, the islands of Cura\u00e7ao and Sint Maarten became constituent countries (Dutch: landen) within the Kingdom of the Netherlands.[13] The island of Aruba is also an constituent country of the Kingdom; Aruba gaining its status in 1986 after seceding from The Netherlands Antilles. \n",
  "Cayman Islands":
    "With a GDP per capita of US$109,684 the Cayman Islands has the highest standard of living in the Caribbean, and one of the highest in the world.[9] Immigrants from over 140 countries and territories reside in the Cayman Islands.[10]\n",
  "Central African Republic":
    "What is today the Central African Republic has been inhabited since at least 8,000 BCE. The country's borders were established by France, which ruled the country as a colony starting in the late 19th century. After gaining independence from France in 1960, the Central African Republic was ruled by a series of autocratic leaders, including an abortive attempt at a monarchy under the regime of Jean-Bedel Bokassa.[9]\n",
  Chad: "Beginning in the 7th millennium BC, human populations moved into the Chadian basin in great numbers. By the end of the 1st millennium AD, a series of states and empires had risen and fallen in Chad's Sahelian strip, each focused on controlling the trans-Saharan trade routes that passed through the region. France conquered the territory by 1920 and incorporated it as part of French Equatorial Africa. In 1960, Chad obtained independence under the leadership of Fran\u00e7ois Tombalbaye. Resentment towards his policies in the Muslim north culminated in the eruption of a long-lasting civil war in 1965. In 1979 the rebels conquered the capital and put an end to the South's hegemony. The rebel commanders then fought amongst themselves until Hiss\u00e8ne Habr\u00e9 defeated his rivals. The Chadian\u2013Libyan conflict erupted in 1978 by the Libyan invasion which stopped in 1987 with a French military intervention (Operation \u00c9pervier). Hiss\u00e8ne Habr\u00e9 was overthrown in turn in 1990 by his general Idriss D\u00e9by. With French support, a modernization of the Chad National Army was initiated in 1991. From 2003, the Darfur crisis in Sudan spilt over the border and destabilised the nation. Already poor, the nation and people struggled to accommodate the hundreds of thousands of Sudanese refugees who live in and around camps in eastern Chad.\n",
  Chile:
    "Chile is a developing country with a high-income economy and is one of the most economically and socially stable nations in South America.[17] Chile also performs well in the region in terms of sustainability of the state and democratic development,[18] and boasts the second lowest homicide rate in the Americas, following only Canada. Chile is a founding member of the United Nations, the Community of Latin American and Caribbean States (CELAC), and the Pacific Alliance, and joined the OECD in 2010.\n",
  China:
    "After decades of struggle, the 1911 Revolution resulted in the overthrow of the monarchy and the establishment of the Republic of China (ROC) the following year. The country under the nascent Beiyang government was unstable and ultimately fragmented during the Warlord Era, which was ended upon the Northern Expedition conducted by the Kuomintang (KMT) to reunify the country. The Chinese Civil War began in 1927, when KMT forces purged members of the rival Chinese Communist Party (CCP), who proceeded to engage in sporadic fighting against the KMT-led Nationalist government. Following the country's invasion by the Empire of Japan in 1937, the KMT and CCP temporarily agreed to a truce in favor of a united front against the Japanese. The Second Sino-Japanese War eventually ended in a Chinese victory; however, atrocities such as the Nanjing Massacre had a lasting impact on the country. The end of war with Japan in 1945 was quickly followed by a resumption of hostilities between the KMT and CCP. In 1949, the resurgent Communists established control over most of the country, proclaiming the People's Republic of China and forcing the Nationalist government to retreat to the island of Taiwan. The country was split, with both sides claiming to be the sole legitimate government of China. Following the implementation of land reforms, further attempts by the PRC to realize communism failed: the Great Leap Forward was largely responsible for the Great Chinese Famine that ended with millions of Chinese people having died, and the subsequent Cultural Revolution was a period of social turmoil and persecution characterized by Maoist populism. Following the Sino-Soviet split, the Shanghai Communiqu\u00e9 in 1972 would precipitate the normalization of relations with the United States. Economic reforms that began in 1978 led by reformists within the CCP moved the country away from a socialist planned economy towards an increasingly capitalist market economy, spurring significant economic growth. The corresponding movement for increased democracy and liberalization stalled after the Tiananmen Square protests and massacre in 1989.\n",
  "Christmas Island":
    "The first European to sight Christmas Island was Richard Rowe of the Thomas in 1615. Captain William Mynors named it on Christmas Day, 25 December 1643. It was first settled in the late 19th century.[13] Christmas Island's geographic isolation and history of minimal human disturbance has led to a high level of endemism among its flora and fauna, which is of interest to scientists and naturalists.[14] The majority (63%) of the island is included in the Christmas Island National Park, which features several areas of primary monsoonal forest. Phosphate, deposited originally as guano, has been mined on the island since 1899.\n",
  "Cocos (Keeling) Islands":
    "The islands were discovered in 1609 by the British sea captain William Keeling, but no settlement occurred until the early 19th century. One of the first settlers was John Clunies-Ross, a Scottish merchant; much of the island's current population is descended from the Malay workers he brought in to work his copra plantation. The Clunies-Ross family ruled the islands as a private fiefdom for almost 150 years, with the head of the family usually recognised as resident magistrate. The British annexed the islands in 1857, and for the next century they were administered from either Ceylon or Singapore. The territory was transferred to Australia in 1955, although until 1979 virtually all of the territory's real estate still belonged to the Clunies-Ross family.\n",
  Colombia:
    "Colombia is one of the world's seventeen megadiverse countries; it has the highest level of biodiversity per square mile in the world and the second-highest level overall.[31] Its territory encompasses Amazon rainforest, highlands, grasslands and deserts. It is the only country in South America with coastlines (and islands) along both the Atlantic and Pacific oceans. Colombia is a key member of major global and regional organizations including the UN, the WTO, the OECD, the OAS, the Pacific Alliance and the Andean Community; it is also a NATO Global Partner[32] and a major non-NATO ally of the United States.[33]\n",
  Comoros:
    "The Comoros were likely first settled by Austronesian/Malagasy peoples, Bantu speakers from East Africa, and seafaring Arab traders.[15] It became part of the French colonial empire during the 19th century, before its independence in 1975. It has experienced more than 20 coups or attempted coups, with various heads of state assassinated.[16][17] Along with this constant political instability, it has one of the highest levels of income inequality of any nation, and ranks in the medium quartile on the Human Development Index.[18] Between 2009 and 2014, about 19% of the population lived below the international poverty line of US$1.90 a day by purchasing power parity.[19]\n",
  "Congo - Brazzaville":
    "The Republic of the Congo is a member of the African Union, the United Nations, La Francophonie, the Economic Community of Central African States, and the Non-Aligned Movement. It has become the 4th-largest oil producer in the Gulf of Guinea, providing the country with a degree of prosperity, with political and economic instability in some areas, and unequal distribution of oil revenue nationwide. Its economy is dependent on the oil sector.[14] and economic growth has slowed since the post-2015 drop in oil prices. Christianity is the most widely professed faith in the country.\n",
  "Congo - Kinshasa":
    "Congo achieved independence from Belgium on 30 June 1960 and was immediately confronted by a series of secessionist movements, the assassination of Prime Minister Patrice Lumumba, and the seizure of power by Mobutu Sese Seko in a 1965 coup d'\u00e9tat. Mobutu renamed the country Zaire in 1971 and imposed a harsh personalist dictatorship until his overthrow in 1997 by the First Congo War.[2] The country then had its name changed back and was confronted by the Second Congo War from 1998 to 2003, which resulted in the deaths of 5.4\u00a0million people.[8][9][10][11] The war ended under President Joseph Kabila, who governed the country from 2001 to 2019 and under whom human rights in the country remained poor and included frequent abuses such as forced disappearances, torture, arbitrary imprisonment and restrictions on civil liberties.[12] Following the 2018 general election, in the country's first peaceful transition of power since independence, Kabila was succeeded as president by F\u00e9lix Tshisekedi, who has served as president since.[13] Since 2015, eastern Congo has been the site of an ongoing military conflict.\n",
  "Cook Islands":
    "The Cook Islands' main population centres are on the island of Rarotonga (10,863 in 2021).[4] The Rarotonga International Airport, the main international gateway to the country, is located on this island. The census of 2021 put the total population at 14,987. There is also a larger population of Cook Islanders in New Zealand and Australia: in the 2018 New Zealand census, 80,532 people said they were Cook Islanders, or of Cook Islands descent.[11] The last Australian census recorded 28,000 Cook Islanders living in Australia, many with Australian citizenship.[12] With over 168,000 visitors to the islands in 2018,[13] tourism is the country's main industry and leading element of its economy, ahead of offshore banking, pearls, and marine and fruit exports.\n",
  "Costa Rica":
    "Costa Rica was inhabited by indigenous peoples before coming under Spanish rule in the 16th century. It remained a peripheral colony of the empire until independence as part of the First Mexican Empire, followed by membership in the Federal Republic of Central America, from which it formally declared independence in 1847. Following the brief Costa Rican Civil War in 1948, it permanently abolished its army in 1949, becoming one of only a few sovereign nations without a standing army.[17][18][19]\n",
  "C\u00f4te d\u2019Ivoire":
    "Ivory Coast is a republic with strong executive power vested in its president. Through the production of coffee and cocoa, it was an economic powerhouse in West Africa during the 1960s and 1970s, then experienced an economic crisis in the 1980s, contributing to a period of political and social turmoil that extended until 2011. Ivory Coast has experienced again high economic growth since the return of peace and political stability in 2011. From 2012 to 2023, the economy grew by an average of 7.1% per year in real terms, the second-fastest rate of economic growth in Africa and fourth-fastest rate in the world.[13] In 2023, Ivory Coast had the second-highest GDP per capita in West Africa, behind Cape Verde.[14] Despite this, as of the most recent survey in 2016, 46.1% of the population continues to be affected by multidimensional poverty.[15] In 2020, Ivory Coast was the world's largest exporter of cocoa beans and had high levels of income for its region.[16] The economy still relies heavily on agriculture, with smallholder cash-crop production predominating.[2]\n",
  Croatia:
    "The Croats arrived in modern-day Croatia in the late 6th century, then part of Roman Illyria. By the 7th century, they had organized the territory into two duchies. Croatia was first internationally recognized as independent on 7 June 879 during the reign of Duke Branimir. Tomislav became the first king by 925, elevating Croatia to the status of a kingdom. During the succession crisis after the Trpimirovi\u0107 dynasty ended, Croatia entered a personal union with Hungary in 1102. In 1527, faced with Ottoman conquest, the Croatian Parliament elected Ferdinand I of Austria to the Croatian throne. In October 1918, the State of Slovenes, Croats, and Serbs, independent from Austria-Hungary, was proclaimed in Zagreb, and in December 1918, it merged into the Kingdom of Yugoslavia. Following the Axis invasion of Yugoslavia in April 1941, most of Croatia was incorporated into a Nazi-installed puppet state, the Independent State of Croatia. A resistance movement led to the creation of the Socialist Republic of Croatia, which after the war became a founding member and constituent of the Socialist Federal Republic of Yugoslavia. On 25 June 1991, Croatia declared independence, and the War of Independence was successfully fought over the next four years.\n",
  Cuba: "Cuba is a socialist state, in which the role of the Communist Party is enshrined in the Constitution. Cuba has an authoritarian government where political opposition is not permitted.[20][21] Censorship is extensive and independent journalism is repressed;[22][23][24] Reporters Without Borders has characterized Cuba as one of the worst countries for press freedom.[25][24] Culturally, Cuba is considered part of Latin America.[26] It is a multiethnic country whose people, culture and customs derive from diverse origins, including the Ta\u00edno Ciboney peoples, the long period of Spanish colonialism, the introduction of enslaved Africans and a close relationship with the Soviet Union during the Cold War.\n",
  "Cura\u00e7ao":
    'The name "Cura\u00e7ao" may originate from the indigenous autonym of its people; this idea is supported by early Spanish accounts referring to the inhabitants as Indios Cura\u00e7aos. Cura\u00e7ao\'s history begins with the Arawak and Caquetio Amerindians; the island becoming a Spanish colony after Alonso de Ojeda\'s 1499 expedition. Though labelled "the useless island" due to its poor agricultural yield and lack of precious metals, it became a strategic cattle ranching area. When the Dutch colonized the island in 1634, they shifted the island\'s focus to trade and shipping, and later made it a hub of the Atlantic slave trade. Members of the Jewish community, fleeing persecution in Europe, settled here and significantly influenced the economy and culture.\n',
  Cyprus:
    "The future of the island became a matter of disagreement between the two prominent ethnic communities, Greek Cypriots and Turkish Cypriots. From the 19th century onwards, the Greek Cypriot population pursued enosis, union with Greece, which became a Greek national policy in the 1950s.[12][13] The Turkish Cypriot population initially advocated the continuation of the British rule, then demanded the annexation of the island to Turkey, and in the 1950s, together with Turkey, established a policy of taksim, the partition of Cyprus and the creation of a Turkish polity in the north.[14] Following nationalist violence in the 1950s, Cyprus was granted independence in 1960.[15] The crisis of 1963\u201364 brought further intercommunal violence between the two communities, displaced more than 25,000 Turkish Cypriots into enclaves[16]:\u200a56\u201359\u200a[17] and brought the end of Turkish Cypriot representation in the republic. On 15 July 1974, a coup d'\u00e9tat was staged by Greek Cypriot nationalists[18][19] and elements of the Greek military junta.[20] This action precipitated the Turkish invasion of Cyprus on 20 July,[21] which led to the capture of the present-day territory of Northern Cyprus and the displacement of over 150,000 Greek Cypriots[22][23] and 50,000 Turkish Cypriots.[24] A separate Turkish Cypriot state in the north was established by unilateral declaration in 1983; the move was widely condemned by the international community, with Turkey alone recognising the new state. These events and the resulting political situation are matters of a continuing dispute.\nCyprus is a major tourist destination in the Mediterranean.[25][26][27] The country has an advanced high-income economy. The Republic of Cyprus has been a member of the Commonwealth since 1961 and was a founding member of the Non-Aligned Movement until it joined the European Union on 1 May 2004.[28] On 1 January 2008, Cyprus joined the eurozone.[29]\n",
  Czechia:
    "The Duchy of Bohemia was founded in the late 9th century under Great Moravia. It was formally recognized as an Imperial State of the Holy Roman Empire in 1002 and became a kingdom in 1198.[16][17] Following the Battle of Moh\u00e1cs in 1526, all of the Crown lands of Bohemia were gradually integrated into the Habsburg monarchy. Nearly a hundred years later, the Protestant Bohemian Revolt led to the Thirty Years' War. After the Battle of White Mountain, the Habsburgs consolidated their rule. With the dissolution of the Holy Roman Empire in 1806, the Crown lands became part of the Austrian Empire.\n",
  Denmark:
    "As of 2013, the Kingdom of Denmark, including the Faroe Islands and Greenland, had a total of 1,419 islands greater than 100 square metres (1,100\u00a0sq\u00a0ft) in area; 443 of these have been named and 78 are inhabited.[14] Spanning a total area of 42,943\u00a0km2 (16,580\u00a0sq\u00a0mi),[15] metropolitan Denmark consists of the northern part of the Jutland peninsula and an archipelago of 406 islands.[16] Of these, the most populated island is Zealand, on which the capital and largest city, Copenhagen, is situated, followed by Funen, the North Jutlandic Island, and Amager.[17] Denmark has flat, arable land, sandy coasts, low elevations, and a temperate climate. Denmark exercises hegemonic influence in the Danish Realm, devolving powers to handle internal affairs. Home rule was established in the Faroe Islands in 1948 and in Greenland in 1979; the latter obtained further autonomy in 2009.[18]\n",
  Djibouti:
    "In antiquity, the territory, together with Ethiopia, Eritrea and Somaliland, was part of the Land of Punt. Nearby Zeila, now in Somaliland, was the seat of the medieval Adal and Ifat Sultanates. In the late 19th century, the colony of French Somaliland was established after the ruling Dir Somali sultans signed treaties with the French,[11][12][13] and its railroad to Dire Dawa (and later Addis Ababa) allowed it to quickly supersede Zeila as the port for southern Ethiopia and the Ogaden.[14] It was renamed the French Territory of the Afars and the Issas in 1967. A decade later, the Djiboutian people voted for independence. This officially marked the establishment of the Republic of Djibouti, named after its capital city. The new state joined the United Nations in its first year.[15][16] In the early 1990s, tensions over government representation led to armed conflict, which ended in a power-sharing agreement in 2000 between the ruling party and the opposition.[1]\n",
  Dominica:
    "The island was settled by the Arawak arriving from South America in the fifth century. The Kalinago displaced the Arawak by the 15th century. Christopher Columbus is said to have passed the island on Sunday, 3 November 1493. It was later colonised by Europeans, predominantly by the French from the 1690s to 1763. The French trafficked slaves from West Africa to Dominica to work on coffee plantations. Great Britain took possession in 1763 after the Seven Years' War, and it gradually established English as its official language. The island gained independence as a republic in 1978.\n",
  "Dominican Republic":
    "The Dominican Republic has the largest economy in the Caribbean and the seventh-largest in Latin America.[23][24] Over the last 25 years, the Dominican Republic has had the fastest-growing economy in the Western Hemisphere \u2013 with an average real GDP growth rate of 5.3% between 1992 and 2018.[25] GDP growth in 2014 and 2015 reached 7.3 and 7.0%, respectively, the highest in the Western Hemisphere.[25] Recent growth has been driven by construction, manufacturing, tourism, and mining. The country is the site of the third largest (in terms of production) gold mine in the world, the Pueblo Viejo mine.[26][27]\n",
  Ecuador:
    "Ecuador is a representative democratic presidential republic and a developing country[21] whose economy is highly dependent on exports of commodities, primarily petroleum and agricultural products. The country is a founding member of the United Nations, Organization of American States, Mercosur, PROSUR, and the Non-Aligned Movement. According to the Center for Economic and Policy Research, between 2006 and 2016, poverty decreased from 36.7% to 22.5% and annual per capita GDP growth was 1.5 percent (as compared to 0.6 percent over the prior two decades). At the same time, the country's Gini index of economic inequality improved from 0.55 to 0.47.[22]\n",
  Egypt:
    "The country was then occupied by the British Empire and gained independence in 1922 as a monarchy. Following the 1952 revolution, Egypt declared itself a republic. For a brief period between 1958 and 1961 Egypt merged with Syria to form the United Arab Republic. Egypt fought several armed conflicts with Israel in 1948, 1956, 1967 and 1973, and occupied the Gaza Strip intermittently until 1967. In 1978, Egypt signed the Camp David Accords, which recognised Israel in exchange for its withdrawal from the occupied Sinai. After the Arab Spring, which led to the 2011 Egyptian revolution and overthrow of Hosni Mubarak, the country faced a protracted period of political unrest; this included the election in 2012 of a brief, short-lived Muslim Brotherhood-aligned Islamist government spearheaded by Mohamed Morsi, and its subsequent overthrow after mass protests in 2013.\n",
  "El Salvador":
    "From the late 19th to the mid-20th century, El Salvador endured chronic political and economic instability characterized by coups, revolts, and a succession of authoritarian rulers. Persistent socioeconomic inequality and civil unrest culminated in the Salvadoran Civil War from 1979 to 1992, fought between the military-led government backed by the United States, and a coalition of left-wing guerrilla groups. The conflict ended with the Chapultepec Peace Accords. This negotiated settlement established a multiparty constitutional republic, which remains in place to this day. \nDuring the civil war and afterwards, large numbers of Salvadorans emigrated to the United States. From 1980 through 2008, nearly one million Salvadorans immigrated to the United States, such that by 2008, they were the sixth largest immigrant group in the US.[19]\n",
  "Equatorial Guinea":
    "The mainland region, R\u00edo Muni, is bordered by Cameroon to the north and Gabon to the south and east. It is the location of Bata, Equatorial Guinea's second largest city, and Ciudad de la Paz, the country's planned future capital. R\u00edo Muni also includes several small offshore islands, such as Corisco, Elobey Grande, and Elobey Chico. The country is a member of the African Union, Francophonie, OPEC, and the CPLP.\n",
  Eritrea:
    "Contemporary Eritrea is a multi-ethnic country with nine recognised ethnic groups, each of which has its own language. The most widely spoken languages are Tigrinya and Arabic, the others being Tigre, Saho, Kunama, Nara, Afar, Beja, Bilen and English.[24] Tigrinya, Arabic and English serve as the three working languages.[2][25][26][27] Most residents speak languages from the Afroasiatic family, either of the Ethiopian Semitic languages or Cushitic branches. Among these communities, the Tigrinyas make up about 50% of the population, with the Tigre people constituting around 30% of inhabitants. In addition, there are several Nilo-Saharan-speaking Nilotic ethnic groups. Most people in the country adhere to Christianity or Islam, with a small minority adhering to traditional faiths.[28]\n",
  Estonia:
    "Present-day Estonia has been inhabited by humans since at least 9,000 BCE. The medieval indigenous population of Estonia was one of the last pagan civilisations in Europe to adopt Christianity following the Northern Crusades in the 13th century.[22] After centuries of successive rule by the Teutonic Order, Denmark, Sweden, and the Russian Empire, a distinct Estonian national identity began to emerge in the mid-19th century. This culminated in the 1918 Estonian Declaration of Independence from the then-warring Russian and German empires. Democratic throughout most of the interwar period, Estonia declared neutrality at the outbreak of World War II, however the country was repeatedly contested, invaded, and occupied; first by the Soviet Union in 1940, then Nazi Germany in 1941, and ultimately reoccupied in 1944 by, and annexed into, the USSR as an administrative subunit (Estonian SSR). Throughout the 1944\u201391 Soviet occupation,[23] Estonia's de jure state continuity was preserved by diplomatic representatives and the government-in-exile. Following the 1988\u201390 bloodless Estonian \"Singing Revolution\" against Soviet rule, the nation's full independence was restored on 20 August 1991.\n",
  Eswatini:
    "Eswatini is a developing country that is classified as having a lower-middle income economy. As a member of the Southern African Customs Union and the Common Market for Eastern and Southern Africa, its main local trading partner is South Africa; to ensure economic stability, Eswatini's currency, the lilangeni, is pegged to the South African rand. Eswatini's major overseas trading partners are the United States[18] and the European Union.[19] The majority of the country's employment is provided by its agricultural and manufacturing sectors. Eswatini is a member of the Southern African Development Community, the African Union, the Commonwealth of Nations, and the United Nations.\n",
  Ethiopia:
    "Ethiopia,[a] officially the Federal Democratic Republic of Ethiopia, is a landlocked country located in the Horn of Africa region of East Africa. It shares borders with Eritrea to the north, Djibouti to the northeast, Somalia to the East, Kenya to the South, South Sudan to the West, and Sudan to the Northwest. Ethiopia covers a land area of 1,112,000 square kilometres (472,000 sq. miles).[14] As of 2024[update], it is home to around 109 million inhabitants, making it the 13th-most populous country in the world, the 2nd-most populous in Africa after Nigeria, and the most populated landlocked country on Earth.[15][16] The national capital and largest city, Addis Ababa, lies several kilometres west of the East African Rift that splits the country into the African and Somali tectonic plates.[17]\n",
  "Falkland Islands":
    "The population (3,662 inhabitants in 2021)[2] is primarily native-born Falkland Islanders, the majority of British descent. Other ethnicities include French, Gibraltarians, and Scandinavians. Immigration from the United Kingdom, the South Atlantic island of Saint Helena, and Chile has reversed a population decline. The predominant (and official) language is English. Under the British Nationality (Falkland Islands) Act 1983, Falkland Islanders are British citizens.\n",
  "Faroe Islands":
    "The Faroe or Faeroe Islands (/\u02c8f\u025b\u0259ro\u028a/ FAIR-oh), or simply the Faroes (Faroese: F\u00f8royar, .mw-parser-output .IPA-label-small{font-size:85%}.mw-parser-output .references .IPA-label-small,.mw-parser-output .infobox .IPA-label-small,.mw-parser-output .navbox .IPA-label-small{font-size:100%}pronounced [\u02c8f\u0153\u0279ja\u0279] \u24d8; Danish: F\u00e6r\u00f8erne [\u02c8fe\u0250\u032f\u02cc\u00f8\u02c0\u0250n\u0259]), are an archipelago in the North Atlantic Ocean and an autonomous territory of the Kingdom of Denmark. The official language of the country is Faroese, which is closely related to and partially mutually intelligible with Icelandic.\n",
  Fiji: "Humans have lived in Fiji since the second millennium BC\u2014first Austronesians and later Melanesians, with some Polynesian influences. Europeans first visited Fiji in the 17th century.[14] In 1874, after a brief period in which Fiji was an independent kingdom, the British established the Colony of Fiji. Fiji operated as a Crown colony until 1970, when it gained independence and became known as the Dominion of Fiji. In 1987, following a series of coups d'\u00e9tat, the military government that had taken power declared it a republic. In a 2006 coup, Commodore Frank Bainimarama seized power. In 2009, the Fijian High Court ruled that the military leadership was unlawful. At that point, President Ratu Josefa Iloilo, whom the military had retained as the nominal head of state, formally abrogated the 1997 Constitution and re-appointed Bainimarama as interim prime minister. Later in 2009, Ratu Epeli Nailatikau succeeded Iloilo as president.[15] On 17 September 2014, after years of delays, a democratic election took place. Bainimarama's FijiFirst party won 59.2% of the vote, and international observers deemed the election credible.[16]\n",
  Finland:
    "Finland was first settled around 9000\u00a0BC after the last Ice Age.[14] During the Stone Age, various cultures emerged, distinguished by different styles of ceramics. The Bronze Age and Iron Ages were marked by contacts with other cultures in Fennoscandia and the Baltic region.[15] From the late 13th century, Finland became part of Sweden as a result of the Northern Crusades. In 1809, as a result of the Finnish War, Finland was captured from Sweden and became an autonomous grand duchy within the Russian Empire. During this period, Finnish art flourished and the independence movement began to take hold. Finland became the first territory in Europe to grant universal suffrage in 1906, and the first in the world to give all adult citizens the right to run for public office.[16][note 2] Following the Russian Revolution of 1917, Finland declared its independence. A civil war was fought in Finland the following year, with the Whites emerging victorious. Finland's status as a republic was confirmed in 1919. During World War II, Finland fought against the Soviet Union in the Winter War and the Continuation War, and later against Nazi Germany in the Lapland War. As a result, it lost parts of its territory but retained its independence.\n",
  France:
    "Metropolitan France was settled during the Iron Age by Celtic tribes known as Gauls before Rome annexed the area in 51 BC, leading to a distinct Gallo-Roman culture. In the Early Middle Ages, the Franks formed the Kingdom of Francia, which became the heartland of the Carolingian Empire. The Treaty of Verdun of 843 partitioned the empire, with West Francia evolving into the Kingdom of France. In the High Middle Ages, France was a powerful but decentralized feudal kingdom, but from the mid-14th to the mid-15th centuries, France was plunged into a dynastic conflict with England known as the Hundred Years' War. In the 16th century, the French Renaissance saw culture flourish and a French colonial empire rise.[13] Internally, France was dominated by the conflict with the House of Habsburg and the French Wars of Religion between Catholics and Huguenots. France was successful in the Thirty Years' War and further increased its influence during the reign of Louis XIV.[14]\n",
  "French Guiana":
    "Since December 2015, both the region and department have been ruled by a single assembly within the framework of a single territorial collectivity, the French Guiana Territorial Collectivity (French: collectivit\u00e9 territoriale de Guyane). This assembly, the French Guiana Assembly (French: assembl\u00e9e de Guyane), replaced the former regional council and departmental council, which were disbanded. The French Guiana Assembly is in charge of regional and departmental government. Its president is Gabriel Serville.\n",
  "French Polynesia":
    "Among its 121 islands and atolls, 75 were inhabited at the 2017 census.[5] Tahiti, which is in the Society Islands group, is the most populous island, being home to nearly 69% of the population of French Polynesia as of 2017[update]. Papeete, located on Tahiti, is the capital of French Polynesia. Although not an integral part of its territory, Clipperton Island was administered from French Polynesia until 2007.\n",
  "French Southern Territories":
    "The territory is sometimes referred to as the French Southern Lands (French: Terres australes fran\u00e7aises) or the French Southern Territories,[7] usually to emphasize non-recognition of French sovereignty over Ad\u00e9lie Land as part of the Antarctic Treaty System.[5]\n",
  Gabon:
    "Gabon is a developing country, ranking 123rd in the Human Development Index. It is one of the wealthiest countries in Africa in terms of per capita income; however, large parts of the population are very poor. Omar Bongo came to office in 1967 and created a dynasty, which stabilized its power through a clientist network, Fran\u00e7afrique.[10]\n",
  Gambia:
    "Arab Muslim merchants traded with native West Africans in The Gambia throughout the 9th and 10th centuries. In 1455, the Portuguese were the first Europeans to enter The Gambia, although they never established significant trade there. The British Empire established a colony in 1765.[12] In 1965, 200 years later, The Gambia gained independence under the leadership of Dawda Jawara. Yahya Jammeh seized power in a bloodless 1994 coup.\n",
  Georgia: "No Information",
  Germany:
    "Settlement in what is now Germany began in the Lower Paleolithic, with various tribes inhabiting it from the Neolithic onward, chiefly the Celts. Various Germanic tribes have inhabited the northern parts of modern Germany since classical antiquity. A region named Germania was documented before AD 100. In 962, the Kingdom of Germany formed the bulk of the Holy Roman Empire. During the 16th century, northern German regions became the centre of the Protestant Reformation. Following the Napoleonic Wars and the dissolution of the Holy Roman Empire in 1806, the German Confederation was formed in 1815.\n",
  Ghana:
    "Ghana is a multi-ethnic country with linguistic and religious groups;[14] while the Akan are the largest ethnic group, they constitute a plurality. Most Ghanaians are Christians (71.3%); almost a fifth are Muslims; a tenth practice traditional faiths or report no religion.[3] Ghana is a unitary constitutional democracy led by a president who is head of state and head of government.[15] For  political stability in Africa, Ghana ranked seventh in the 2012 Ibrahim Index of African Governance and fifth in the 2012 Fragile States Index. It has maintained since 1993 one of the freest and most stable governments on the continent, and it performs relatively well in healthcare, economic growth, and human development,[13][16] so that it has a significant influence in West Africa and Africa as a whole.[17] Ghana is highly integrated in international affairs, being a founding member of the Non-Aligned Movement and the African Union, and a member of the Economic Community of West African States, the Group of 24 and the Commonwealth of Nations.[18]\n",
  Gibraltar:
    "The sovereignty of Gibraltar is a point of contention in Anglo-Spanish relations, as Spain asserts a claim to the territory.[13][14] Gibraltarians overwhelmingly rejected proposals for Spanish sovereignty in a 1967 referendum, and for shared sovereignty in a 2002 referendum.[15][16] Nevertheless, Gibraltar maintains close economic and cultural links with Spain, with many Gibraltarians speaking Spanish as well as a local dialect known as Llanito.[17][18][19]\n",
  Greece:
    "Greece is considered the cradle of Western civilization, being the birthplace of democracy, Western philosophy, Western literature, historiography, political science, major scientific and mathematical principles, theatre, and the Olympic Games. From the eighth century BC, the Greeks were organised into various independent city-states known as poleis (singular polis) that spanned the Mediterranean and Black seas. Philip II of Macedon united most of present-day Greece in the fourth century BC, with his son Alexander the Great rapidly conquering much of the known ancient world from the eastern Mediterranean to northwestern India. The subsequent Hellenistic period saw the height of Greek culture and influence in antiquity. Greece was annexed by Rome in the second century BC, becoming an integral part of the Roman Empire and its continuation, the Byzantine Empire, which was predominately Greek in culture and language. The Greek Orthodox Church, which emerged in the first century AD, helped shape modern Greek identity and transmitted Greek traditions to the wider Orthodox world. After the Fourth Crusade in 1204, Latin possessions were established in parts of the Greek peninsula, but most of the area fell under Ottoman rule by the mid-15th century.\n",
  Greenland:
    "Though a part of the continent of North America, Greenland has been politically and culturally associated with Europe (specifically Norway and Denmark, the colonial powers) for more than a millennium, beginning in 986.[18] Greenland has been inhabited at intervals over at least the last 4,500 years by circumpolar peoples whose forebears migrated there from what is now Canada.[19][20] Norsemen settled the uninhabited southern part of Greenland beginning in the 10th century (having previously settled Iceland), and the 13th century saw the arrival of Inuit. Though under continuous influence of Norway and Norwegians, Greenland was not formally under the Norwegian crown until 1261. The Norse colonies disappeared in the late 15th century, after Norway was hit by the Black Death and entered a severe decline.\n",
  Grenada:
    "12\u00b007\u2032N 61\u00b040\u2032W\ufeff / \ufeff12.117\u00b0N 61.667\u00b0W\ufeff / 12.117; -61.667\n",
  Guadeloupe:
    "Christopher Columbus visited Guadeloupe in 1493 and gave the island its name. The official language is French; Antillean Creole is also spoken.[4][5]\n",
  Guam: "Before World War II, Guam was one of five American jurisdictions in the Pacific Ocean, along with Wake Island in Micronesia, American Samoa and Hawaii in Polynesia, and the Philippines. On December 8, 1941, hours after the attack on Pearl Harbor, Guam was captured by the Japanese, who occupied the island for two and a half years before American forces recaptured it on July 21, 1944, which is commemorated there as Liberation Day.[9] Since the 1960s, Guam's economy has been supported primarily by tourism and the U.S. military, for which Guam is a major strategic asset.[10] Its future political status has been a matter of significant discussion, with public opinion polls indicating a strong preference for American statehood.[11][12]\n",
  Guatemala:
    "The territory of modern Guatemala hosted the core of the Maya civilization, which extended across Mesoamerica; in the 16th century, most of this was conquered by the Spanish and claimed as part of the viceroyalty of New Spain. Guatemala attained independence from Spain and Mexico in 1821. From 1823 to 1841, it was part of the Federal Republic of Central America.\nFor the latter half of the 19th century, Guatemala suffered instability and civil strife. From the early 20th century, it was ruled by a series of dictators backed by the United States. In 1944, authoritarian leader Jorge Ubico was overthrown by a pro-democratic military coup, initiating a\u00a0decade-long revolution that led to social and economic reforms. In 1954, a\u00a0US-backed military coup ended the revolution and installed a dictatorship.[8]\nFrom 1960 to 1996, Guatemala endured a bloody civil war fought between the US-backed government and leftist rebels, including genocidal massacres of the Maya population perpetrated by the Guatemalan military.[9][10][11] The United Nations negotiated a peace accord, resulting in economic growth and successive democratic elections.\n",
  Guernsey:
    "Guernsey was part of the Duchy of Normandy until 1204, when the Channel Islands remained loyal to the English crown, splitting from mainland Normandy. In 1290, the Channel Islands were divided administratively and Guernsey became part of the Bailiwick of Guernsey. During the Second World War, Guernsey was invaded and occupied by Nazi Germany. After five years of occupation, the island was liberated on 9 May 1945, which is celebrated annually as Liberation Day.\n",
  Guinea:
    "Muslims represent 90% of the population.[8][21][22] The country is divided into four geographic regions: Maritime Guinea on the Atlantic coast, the Fouta Djallon or Middle Guinea highlands, the Upper Guinea savanna region in the northeast, and the Guin\u00e9e foresti\u00e8re region of tropical forests. French, the official language of Guinea, is a language of communication in schools, in government administration, and the media. More than 24 indigenous languages are spoken and the largest are Susu, Pular, and Maninka, which dominate respectively in Maritime Guinea, Fouta Djallon, and Upper Guinea, while Guin\u00e9e foresti\u00e8re is ethnolinguistically diverse. Guinea's economy is mostly dependent on agriculture and mineral production.[23] It is the world's second largest producer of bauxite, and has deposits of diamonds and gold.[24] As of the most recent survey in 2018, 66.2% of the population is affected by multidimensional poverty and an additional 16.4% vulnerable to it.[25] The country was at the core of the Western African Ebola virus epidemic.\n",
  "Guinea-Bissau":
    "The Bissagos, islands off the coast of Guinea-Bissau, were officially conquered in 1936, ensuring Portuguese control of both the mainland and islands of the region.[13]\n",
  Guyana:
    'The region known as "the Guianas" consists of the large shield landmass north of the Amazon River and east of the Orinoco River known as the "land of many waters". Nine indigenous tribes reside in Guyana: the Wai Wai, Macushi, Patamona, Lokono, Kalina, Wapishana, Pemon, Akawaio and Warao. Historically dominated by the Lokono and Kalina tribes, Guyana was colonised by the Dutch before coming under British control in the late 18th century. It was governed as British Guiana with a mostly plantation-style economy until the 1950s. It gained independence in 1966 and officially became a republic within the Commonwealth of Nations in 1970. The legacy of British rule is reflected in the country\'s political administration, lingua franca and diverse population, which includes Indian, African, Indigenous, Chinese, Portuguese, other European, and various multiracial groups.\n',
  Haiti:
    "Following a coup d'\u00e9tat in 2004, the United Nations intervened to stabilize the country. In 2010, Haiti suffered a catastrophic earthquake, followed by a deadly cholera outbreak. With its deteriorating economic situation,[31] the country has experienced a socioeconomic and political crisis marked by riots and protests, widespread hunger, and increased gang activity.[32] As of May 2024, Haiti has no remaining elected government officials and has been described as a failed state.[33][34]\n",
  "Heard & McDonald Islands":
    "The islands are among the most remote places on Earth: They are located about 4,100 kilometres (2,200 nautical miles) southwest of Perth,[6] 3,850\u00a0km (2,080\u00a0nmi) southwest of Cape Leeuwin, Australia, 4,200\u00a0km (2,300\u00a0nmi) southeast of South Africa, 3,830\u00a0km (2,070\u00a0nmi) southeast of Madagascar, 1,630\u00a0km (880\u00a0nmi) north of Antarctica, and 450\u00a0km (240\u00a0nmi) southeast of the Kerguelen Islands (part of French Southern and Antarctic Lands).[7]\n",
  Honduras:
    "The nation's economy is primarily agricultural, making it especially vulnerable to natural disasters such as Hurricane Mitch in 1998.[8] The lower class is primarily agriculturally based while wealth is concentrated in the country's urban centers.[9] Honduras has a Human Development Index of 0.625, classifying it as a nation with medium development.[10] When adjusted for income inequality, its Inequality-adjusted Human Development Index is 0.443.[10]\n",
  "Hong Kong SAR China":
    "Originally a sparsely populated area of farming and fishing villages,[18][19] the territory is now one of the world's most significant financial centres and commercial ports. Hong Kong is the world's third-ranked global financial centre (behind New York City and London), ninth-largest exporter, and eighth-largest importer. Its currency, the Hong Kong dollar, is the ninth most traded currency in the world. Home to the seventh-highest number of billionaires of any city in the world, Hong Kong has the largest number of ultra high-net-worth individuals.[20] Although the city has one of the highest per capita incomes in the world, severe income inequality exists among the population. Despite being the city with the most skyscrapers in the world, housing in Hong Kong is consistently in high demand.\n",
  Hungary:
    "Prior to the foundation of the Hungarian state, various peoples settled in the territory of present-day Hungary, most notably the Celts, Romans, Huns, Germanic peoples, Avars and Slavs. The Principality of Hungary was established in the late 9th century by \u00c1lmos and his son \u00c1rp\u00e1d through the conquest of the Carpathian Basin.[11][12] King Stephen I ascended the throne in 1000, converting his realm to a Christian kingdom. The medieval Kingdom of Hungary was a European power, reaching its height in the 14th\u201315th centuries.[13] After a long period of Ottoman wars, Hungary's forces were defeated at the Battle of Moh\u00e1cs and its capital was captured in 1541, opening roughly a 150 years long period when the country was divided into three parts: Royal Hungary, loyal to the Habsburgs; Ottoman Hungary; and the largely independent Principality of Transylvania. The reunited Hungary came under Habsburg rule at the turn of the 18th century, fighting a war of independence in 1703\u20131711, and a war of independence in 1848\u20131849 until a compromise allowed the formation of the Austro-Hungarian Monarchy in 1867, a major power into the early 20th century.[14] Austria-Hungary collapsed after World War I, and the subsequent Treaty of Trianon in 1920 established Hungary's current borders, resulting in the loss of 71% of its historical territory, 58% of its population, and 32% of its ethnic Hungarians.[15][16][17]\n",
  Iceland:
    "According to the ancient manuscript Landn\u00e1mab\u00f3k, the settlement of Iceland began in 874 AD, when the Norwegian chieftain Ing\u00f3lfr Arnarson became the island's first permanent settler.[15] In the following centuries, Norwegians, and to a lesser extent other Scandinavians, immigrated to Iceland, bringing with them thralls (i.e., slaves or serfs) of Gaelic origin.\n",
  India:
    "In the early medieval era, Christianity, Islam, Judaism, and Zoroastrianism became established on India's southern and western coasts.[44]\nMuslim armies from Central Asia intermittently overran India's northern plains,[45]\neventually founding the Delhi Sultanate, and drawing northern India into the cosmopolitan networks of medieval Islam.[46]\nIn the 15th century, the Vijayanagara Empire created a long-lasting composite Hindu culture in south India.[47]\nIn the Punjab, Sikhism emerged, rejecting institutionalised religion.[48]\nThe Mughal Empire, in 1526, ushered in two centuries of relative peace,[49]\nleaving a legacy of luminous architecture.[l][50]\nGradually expanding rule of the British East India Company followed, turning India into a colonial economy, but also consolidating its sovereignty.[51] British Crown rule began in 1858. The rights promised to Indians were granted slowly,[52][53] but technological changes were introduced, and modern ideas of education and the public life took root.[54] A pioneering and influential nationalist movement emerged, which was noted for nonviolent resistance and became the major factor in ending British rule.[55][56] In 1947 the British Indian Empire was partitioned into two independent dominions,[57][58][59][60] a Hindu-majority Dominion of India and a Muslim-majority Dominion of Pakistan, amid large-scale loss of life and an unprecedented migration.[61]\n",
  Indonesia:
    "Indonesia is a presidential republic with an elected legislature. It has 38 provinces, of which nine have special autonomous status. The country's largest city, Jakarta, is the world's second-most-populous urban area. Indonesia shares land borders with Papua New Guinea, East Timor, and the eastern part of Malaysia, as well as maritime borders with Singapore, Peninsula Malaysia, Vietnam, Thailand, the Philippines, Australia, Palau, and India. Despite its large population and densely populated regions, Indonesia has vast areas of wilderness that support one of the world's highest levels of biodiversity.\n",
  Iran: "In the 16th century, the native Safavids re-established a unified Iranian state with Twelver Shi'ism as the official religion. During the Afsharid Empire in the 18th century, Iran was a leading world power, though by the 19th century, it had lost significant territory through conflicts with the Russian Empire. The early 20th century saw the Persian Constitutional Revolution and the establishment of the Pahlavi dynasty. Attempts by Mohammad Mosaddegh to nationalize the oil industry led to an Anglo-American coup in 1953. After the Iranian Revolution, the monarchy was overthrown in 1979 and the Islamic Republic of Iran was established by Ruhollah Khomeini, who became the country's first Supreme Leader. In 1980, Iraq invaded Iran, sparking the eight-year-long Iran\u2013Iraq War, which ended in stalemate.\n",
  Iraq: "Iraq remained under Ottoman rule until the end of World War I, after which Mandatory Iraq was established by the British Empire, ruled by Faisal I. Iraq gained independence in 1932 as the Kingdom of Iraq. It became a republic in 1958, led by Abdul Karim Qasim and then by Abdul Salam Arif and Abdul Rahman Arif. Following the 1968 revolution, the Iraqi Ba'ath Party came to power, under the leadership of Ahmed Hassan al-Bakr, followed by Saddam Hussein, who led the Ba'ath regime from 1968 to 2003. The 21st century has seen the country facing challenges, which came after the 2003 invasion of Iraq, that overthrew the government of Saddam and the protracted Iraq War, followed by the subsequent efforts to rebuild the country amidst sectarian violence and the rise of the Islamic State. Today the Post-war conflict in Iraq continues at a lower scale, which has been obstacle to the country's stability.[20][21]\n",
  Ireland:
    "The geography of Ireland comprises relatively low-lying mountains surrounding a central plain, with several navigable rivers extending inland. Its lush vegetation is a product of its mild but changeable climate which is free of extremes in temperature. Much of Ireland was woodland until the end of the Middle Ages. Today, woodland makes up about 10% of the island, compared with a European average of over 33%,[12] with most of it being non-native conifer plantations.[13][14] The Irish climate is influenced by the Atlantic Ocean and thus very moderate,[15] and winters are milder than expected for such a northerly area, although summers are cooler than those in continental Europe. Rainfall and cloud cover are abundant.\n",
  "Isle of Man":
    "In 1266, King Magnus VI of Norway sold his suzerainty over Man to King Alexander III of Scotland under the Treaty of Perth.[14] After a period of alternating rule by the Kings of Scotland and England, the island came under the feudal lordship of the English Crown in 1399. The lordship revested in the British Crown in 1765, but the island did not become part of the 18th-century Kingdom of Great Britain, nor of its successors, the United Kingdom of Great Britain and Ireland and the present-day United Kingdom of Great Britain and Northern Ireland. It has always retained its internal self-government. In 1881, the Isle of Man Parliament, Tynwald, became the first national legislative body in the world to give women the right to vote in a general election, although this excluded married women.[15][b]\n",
  Israel:
    "The State of Israel declared its establishment on 14 May 1948. The armies of neighboring Arab states invaded the area of the former Mandate the next day, beginning the First Arab\u2013Israeli War. Subsequent armistice agreements established Israeli control over 77 percent of the former Mandate territory.[26][27][28] The majority of Palestinian Arabs were either expelled or fled in what is known as the Nakba, with those remaining becoming the new state's main minority.[29][30][31] Over the following decades, Israel's population increased greatly as the country received an influx of Jews who emigrated, fled or were expelled from the Muslim world.[32][33] Following the 1967 Six-Day War Israel occupied the West Bank, Gaza Strip, Egyptian Sinai Peninsula and Syrian Golan Heights. Israel established and continues to expand settlements across the illegally occupied territories, contrary to international law, and has effectively annexed East Jerusalem and the Golan Heights in moves largely unrecognized internationally. After the 1973 Yom Kippur War, Israel signed peace treaties with Egypt\u2014returning the Sinai in 1982\u2014and Jordan. In the 2020s, it normalized relations with more Arab countries. However, efforts to resolve the Israeli\u2013Palestinian conflict after the interim Oslo Accords have not succeeded, and the country has engaged in several wars and clashes with Palestinian militant groups.   Israel's practices in its occupation of the Palestinian territories have drawn sustained international criticism along with accusations that it has committed war crimes and crimes against humanity against the Palestinian people from human rights organizations and United Nations officials.\n",
  Italy:
    "In antiquity, Italy was home to numerous peoples; the city of Rome, founded as a Kingdom, became a Republic that conquered the Mediterranean world and ruled it for centuries as an Empire.[17] With the spread of Christianity, Rome became the seat of the Catholic Church and the Papacy. During the Early Middle Ages, Italy experienced the fall of the Western Roman Empire and inward migration from Germanic tribes. By the 11th century, Italian city-states and maritime republics expanded, bringing renewed prosperity through commerce and laying the groundwork for modern capitalism.[18][19] The Italian Renaissance flourished during the 15th and 16th centuries and spread to the rest of Europe. Italian explorers discovered new routes to the Far East and the New World, leading the European Age of Discovery. However, centuries of rivalry and infighting between city-states left the peninsula divided.[20] During the 17th and 18th centuries, Italian economic importance waned significantly.[21]\n",
  Jamaica:
    "With 2.8 million people,[10][11] Jamaica is the third most populous Anglophone country in the Americas (after the United States and Canada), and the fourth most populous country in the Caribbean. Kingston is the country's capital and largest city. Most Jamaicans are of Sub-Saharan African ancestry, with significant European, East Asian (primarily Chinese), Indian, Lebanese, and mixed-race minorities.[9] Because of a high rate of emigration for work since the 1960s, there is a large Jamaican diaspora, particularly in Canada, the United Kingdom, and the United States. The country has a global influence that belies its small size; it was the birthplace of the Rastafari religion, reggae music (and such associated genres as dub, ska and dancehall), and it is internationally prominent in sports, including cricket, sprinting, and athletics.[12][13][14][15] Jamaica has sometimes been considered the world's least populous cultural superpower.[16][17][18][19]\n",
  Japan:
    "Japan is a constitutional monarchy with a bicameral legislature, the National Diet. A great power and the only Asian member of the G7, Japan has constitutionally renounced its right to declare war, but maintains one of the world's strongest militaries. A highly developed country with one of the world's largest economies, Japan is a global leader in science and technology and the automotive, robotics, and electronics industries. It has one of the world's highest life expectancies, though it is undergoing a population decline. Japan's culture is well known around the world, including its art, cuisine, film, music, and popular culture, which includes prominent comics, animation, and video game industries.\n",
  Jersey:
    "Jersey was part of the Duchy of Normandy, whose dukes became kings of England from 1066. After Normandy was lost by the kings of England in the 13th century, and the ducal title surrendered to France, Jersey remained loyal to the English Crown, though it never became part of the Kingdom of England. Between then and the end of the Napoleonic Wars, Jersey was at the frontline of Anglo-French Wars and was invaded a number of times, leading to the construction of fortifications such as Mont Orgueil Castle and a thriving smuggling industry. During the Second World War, the island was invaded and occupied for five years by Nazi Germany. The island was liberated on 9 May 1945, which is now celebrated as the island's national day.[20]\n",
  Jordan:
    "Jordan is a semi-arid country, covering an area of 89,342\u00a0km2 (34,495\u00a0sq\u00a0mi) with a population of 11.5\u00a0million, making it the eleventh-most populous Arab country. The dominant majority, or around 95% of the country's population, is Sunni Muslim, with the rest being mostly Arab Christian. Jordan was mostly unscathed by the violence that swept the region following the Arab Spring in 2010. From as early as 1948, Jordan has accepted refugees from multiple neighbouring countries in conflict. An estimated 2.1\u00a0million Palestinian refugees, most of whom hold Jordanian citizenship, as well as 1.4\u00a0million Syrian refugees, were residing in Jordan as of 2015.[4] The kingdom is also a refuge for thousands of Christian Iraqis fleeing persecution.[8][9] While Jordan continues to accept refugees, the large Syrian influx during the 2010s has placed substantial strain on national resources and infrastructure.[10]\n",
  Kazakhstan:
    "Kazakhstan dominates Central Asia both economically and politically, accounting for 60 percent of the region's GDP, primarily through its oil and gas industry; it also has vast mineral resources.[16] Kazakhstan also has the highest Human Development Index ranking in the region. It is a unitary constitutional republic;[17] however, its government is authoritarian.[18][19] Nevertheless, there have been incremental efforts at democratization and political reform since the resignation of Nursultan Nazarbayev in 2019, who had led the country since independence. Kazakhstan is a member state of the United Nations, World Trade Organization, Commonwealth of Independent States, Shanghai Cooperation Organisation, Eurasian Economic Union, Collective Security Treaty Organization, Organization for Security and Cooperation in Europe, Organization of Islamic Cooperation, Organization of Turkic States, and International Organization of Turkic Culture.\n",
  Kenya:
    "European contact began in 1500 AD with the Portuguese Empire, and effective colonisation of Kenya began in the 19th century during the European exploration of Africa. Modern-day Kenya emerged from a protectorate established by the British Empire in 1895 and the subsequent Kenya Colony, which began in 1920. Numerous disputes between the UK and the colony led to the Mau Mau revolution, which began in 1952, and the declaration of independence in 1963. After independence, Kenya remained a member of the Commonwealth of Nations. The current constitution was adopted in 2010 and replaced the 1963 independence constitution.\n",
  Kiribati:
    "Kiribati gained its independence from the United Kingdom, becoming a sovereign state in 1979. The capital, South Tarawa, now the most populated area, consists of a number of islets, connected by a series of causeways. These comprise about half the area of Tarawa Atoll. Prior to its independence, the country had exported phosphate, but those mines are no longer viable. Fisheries and export of copra drive much of the economy. Kiribati is one of the least developed countries in the world and is highly dependent on international aid for its economy.\n",
  Kuwait:
    "Like most other Arab states in the Persian Gulf, Kuwait is an emirate; the emir is the head of state and the ruling Al Sabah family dominates the country's political system. Kuwait's official state religion is Islam, specifically the Maliki school of Sunni Islam. Kuwait is a high-income economy, backed by the world's sixth largest oil reserves. \n",
  Kyrgyzstan:
    "After independence, Kyrgyzstan was officially a unitary presidential republic. After the Tulip Revolution it became a unitary parliamentary republic; however, it gradually developed an executive president and was governed as a semi-presidential republic before reverting to a presidential system in 2021. Throughout its existence, the country has continued to endure ethnic conflicts,[18][19] revolts,[20] economic troubles,[21][22] transitional governments[23] and political conflict.[24]\n",
  Laos: "Laos traces its historic and cultural identity to Lan Xang, which existed from the 13th century to the 18th century as one of the largest kingdoms in Southeast Asia.[13] Because of its central geographical location in Southeast Asia, the kingdom became a hub for overland trade and became wealthy economically and culturally.[13] After a period of internal conflict, Lan Xang broke into the Kingdom of Luang Phrabang, the Kingdom of Vientiane and the Kingdom of Champasak. In 1893, the three kingdoms were united under a French protectorate.\n",
  Latvia:
    "After centuries of Teutonic, Swedish, Polish-Lithuanian, and Russian rule, the independent Republic of Latvia was established on 18 November 1918 after breaking away from the German Empire in the aftermath of World War I.[3] The country became increasingly autocratic after the coup in 1934 established the dictatorship of K\u0101rlis Ulmanis.[19] Latvia's de facto independence was interrupted at the outset of World War II, beginning with Latvia's forcible incorporation into the Soviet Union, followed by the invasion and occupation by Nazi Germany in 1941 and the re-occupation by the Soviets in 1944, which formed the Latvian SSR for the next 45 years. As a result of extensive immigration during the Soviet occupation, ethnic Russians became the most prominent minority in the country. The peaceful Singing Revolution started in 1987 among the Baltic Soviet republics and ended with the restoration of both de facto and officially independence on 21 August 1991.[20] Latvia has since been a democratic unitary parliamentary republic.\n",
  Lebanon:
    "After the dissolution of the Ottoman Empire following World War I, Lebanon came under the Mandate for Syria and the Lebanon, administered by France, which established Greater Lebanon. French rule weakened significantly after the German invasion of France in 1940. By 1943, Lebanon had gained independence from Free France and established a distinct form of confessionalist government, with the state's major religious groups being apportioned specific political powers. The new Lebanese state was relatively stable after independence,[16] but this was ultimately shattered by the outbreak of the Lebanese Civil War (1975\u20131990). Lebanon was also subjugated by two military occupations: Syria from 1976 to 2005 and Israel from 1985 to 2000. Since the end of the conflicts, there have been extensive efforts to revive the economy and rebuild national infrastructure.[17]\n",
  Lesotho:
    "Lesotho was formed in 1824 by King Moshoeshoe I. Continuous encroachments by Dutch settlers made the King enter into an agreement with the British Empire to become a protectorate in 1868 and, in 1884, a crown colony. It achieved independence in 1966, and was subsequently ruled by the Basotho National Party (BNP) for two decades. Its constitutional government was restored in 1993 after seven years of military rule. King Moshoeshoe II was exiled in 1990 but returned in 1992 and was reinstated in 1995. One year later, Moshoeshoe II died and his son Letsie III took the throne, which he still holds.[8]\n",
  Liberia:
    "Liberia was the first African republic to proclaim its independence and is Africa's first and oldest modern republic. Along with Ethiopia, it was one of the two African countries to maintain its sovereignty and independence during the European colonial \"Scramble for Africa\". During World War\u00a0II, Liberia supported the U.S. war effort against Nazi Germany and in turn received considerable American investment in infrastructure, which aided the country's wealth and development.[11] President William Tubman encouraged economic and political changes that heightened the country's prosperity and international profile; Liberia was a founding member of the League of Nations, United Nations, and the Organisation of African Unity.\n",
  Libya:
    "During World War II, Libya was an area of warfare in the North African Campaign. The Italian population then went into decline. Libya became independent as a kingdom in 1951. A bloodless military coup in 1969, initiated by a coalition led by Colonel Muammar Gaddafi, overthrew King Idris I and created a republic.[12] Gaddafi was often described by critics as a dictator, and was one of the world's longest serving non-royal leaders, ruling for 42 years.[13] He ruled until being overthrown and killed during the 2011 Libyan Civil War, which was part of the wider Arab Spring, with authority transferred to the National Transitional Council then to the elected General National Congress. Since 2011, Libya has been involved in a political and humanitarian crisis, and by 2014, two rival authorities claimed to govern Libya,[14][15][16] which led to a second civil war, with parts of Libya split between the Tobruk and Tripoli-based governments as well as various tribal and Islamist militias.[17] The two main warring sides signed a permanent ceasefire in 2020, and a unity government took authority to plan for democratic elections, though political rivalries continue to delay this.[18] In March 2022, the House of Representatives ceased recognising the Government of National Unity and proclaimed an alternative government, the Government of National Stability (GNS). Both governments have been functioning simultaneously since then, which has led to dual power in Libya. The international community continues to recognise the unity government as the legitimate government of the country.[19]\n",
  Liechtenstein:
    "Liechtenstein is divided into 11 municipalities. Its capital is Vaduz, and its largest municipality is Schaan. It is a member of the United Nations, the European Free Trade Association, and the Council of Europe. It is not a member state of the European Union, but it participates in both the Schengen Area and the European Economic Area. It has a customs union and a monetary union with Switzerland, with its usage of the Swiss franc. Politically, a constitutional referendum in 2003 granted the monarch greater powers, after he threatened to leave the country should the referendum fail. These powers include being able to dismiss the government, nominate judges and veto legislation.[17]\n",
  Lithuania:
    "Lithuania (/\u02ccl\u026a\u03b8ju\u02c8e\u026ani\u0259/ \u24d8 LITH-ew-AY-nee-\u0259;[13] Lithuanian: Lietuva [l\u02b2i\u0259t\u028a\u02c8v\u0250]), officially the Republic of Lithuania (Lithuanian: Lietuvos Respublika [l\u02b2i\u0259t\u028a\u02c8vo\u02d0s r\u02b2\u025bs\u02c8p\u028abl\u02b2\u026ak\u0250]), is a country in the Baltic region of Europe.[a] It is one of three Baltic states and lies on the eastern shore of the Baltic Sea. It borders Latvia to the north, Belarus to the east and south, Poland to the south, and the Russian semi-exclave of Kaliningrad Oblast to the southwest, with a maritime border with Sweden to the west. Lithuania covers an area of 65,300\u00a0km2 (25,200\u00a0sq\u00a0mi), with a population of 2.88 million. Its capital and largest city is Vilnius; other major cities are Kaunas, Klaip\u0117da, \u0160iauliai and Panev\u0117\u017eys. Lithuanians belong to the ethnolinguistic group of the Balts and speak Lithuanian.\n",
  Luxembourg:
    "With an area of 2,586 square kilometers (998\u00a0sq\u00a0mi), Luxembourg is Europe's seventh-smallest country.[15] In 2024, it had a population of 672,050, which makes it one of the least-populated countries in Europe,[16] albeit with the highest population growth rate;[17] foreigners account for nearly half the population.[18] Luxembourg is a representative democracy headed by a constitutional monarch, Grand Duke Henri, making it the world's only remaining sovereign grand duchy.\n",
  "Macao SAR China": "No Information",
  Madagascar:
    "Following the prehistoric breakup of the supercontinent Gondwana, Madagascar split from Africa during the Early Jurassic, around 180 million years ago, and split from the Indian subcontinent around 90\u00a0million years ago,[15] allowing native plants and animals to evolve in relative isolation; consequently, it is a biodiversity hotspot and one of the world's 17 megadiverse countries, with over 90% of wildlife being endemic. The island has a subtropical to tropical maritime climate. Madagascar was first settled during or before the mid-first millennium AD by Austronesian peoples,[16] presumably arriving on outrigger canoes from present-day Indonesia.[17][18][19] These were joined around the ninth century AD by Bantu migrants crossing the Mozambique Channel from East Africa.[20] Other groups continued to settle on Madagascar over time, each one making lasting contributions to Malagasy cultural life. Consequently, there are 18 or more classified peoples of Madagascar, the most numerous being the Merina of the central highlands.\n",
  Malawi:
    "Malawi is one of the world's least-developed countries. The economy is heavily based on agriculture, and it has a largely rural and rapidly growing population. The Malawian government depends heavily on outside aid to meet its development needs, although the amount needed (and the aid offered) has decreased since 2000. The Malawian government faces challenges in its efforts to build and expand the economy, to improve education, healthcare, and environmental protection, and to become financially independent despite widespread unemployment. Since 2005, Malawi has developed several policies that focus on addressing these issues, and the country's outlook appears to be improving: key indicators of progress in the economy, education, and healthcare were seen in 2007 and 2008.\n",
  Malaysia:
    "The country has its origins in the Malay kingdoms, which, from the 18th century on, became subject to the British Empire, along with the British Straits Settlements protectorate. During World War Two, British Malaya, along with other nearby British and American colonies, was occupied by the Empire of Japan.[16] Following three years of occupation, Peninsular Malaysia was briefly unified as the Malayan Union in 1946 until 1948 when it was restructured as the Federation of Malaya. The country achieved independence on 31 August 1957. On 16 September 1963, independent Malaya united with the then British crown colonies of North Borneo, Sarawak, and Singapore to become Malaysia. In August 1965, Singapore was expelled from the federation and became a separate, independent country.[17]\n",
  Maldives:
    "The Maldives has been inhabited for over 2,500 years. Documented contact with the outside world began around 947 AD when Arab travelers began visiting the islands. In the 12th century, partly due to the importance of the Arabs and Persians as traders in the Indian Ocean, Islam reached the Maldivian Archipelago.[14] The Maldives was soon consolidated as a sultanate, developing strong commercial and cultural ties with Asia and Africa. From the mid-16th century, the region came under the increasing influence of European colonial powers, with the Maldives becoming a British protectorate in 1887. Independence from the United Kingdom came in 1965, and a presidential republic was established in 1968 with an elected People's Majlis. The ensuing decades have seen political instability, efforts at democratic reform,[15] and environmental challenges posed by climate change and rising sea levels.[16] The Maldives became a founding member of the South Asian Association for Regional Cooperation (SAARC).\n",
  Mali: "Mali was part of three successive powerful and wealthy West African empires that controlled trans-Saharan trade: the Ghana Empire (for which Ghana is named), the Mali Empire (for which Mali is named), and the Songhai Empire. At its peak in 1300, the Mali Empire was the wealthiest country in Africa[20] with its 14th-century emperor Mansa Musa believed to be one of the wealthiest individuals in history.[21][22][23] Besides being a hub of trade and mining, medieval Mali was a centre of Islam, culture and knowledge, with Timbuktu becoming a renowned place of learning with its university, one of the oldest in the world and still active. The expanding Songhai Empire absorbed the empire in 1468,[not verified in body] followed by a Saadian army which defeated the Songhai in 1591. In the late 19th century, during the Scramble for Africa, France seized control of Mali, making it a part of French Sudan; as the Sudanese Republic, a brief federation with Senegal was formed, achieving independence in 1960. After Senegal's withdrawal, the Republic of Mali was established. After a long period of one-party rule, a coup in 1991 led to a new constitution and the establishment of Mali as a democratic, multi-party state.\n",
  Malta:
    "Malta has been inhabited since about 5900 BC.[25] Its location in the centre of the Mediterranean has historically given it great geostrategic importance, with a succession of powers having ruled the islands and shaped its culture and society.[26] These include the Phoenicians, Carthaginians, Greeks, and Romans in antiquity; the Arabs, Normans, and Aragonese during the Middle Ages; and the Knights Hospitaller, French, and British in the modern era.[27][28] Malta came under British rule in the early 19th century and served as the headquarters for the British Mediterranean Fleet. It was besieged by the Axis powers during World War II and was an important Allied base for North Africa and the Mediterranean.[29][30] Malta achieved independence in 1964,[31] and established its current parliamentary republic in 1974. It has been a member state of the Commonwealth of Nations and the United Nations since independence; it joined the European Union in 2004 and the eurozone monetary union in 2008. \n",
  "Marshall Islands":
    "Austronesian settlers reached the Marshall Islands as early as the 2nd millennium BC and introduced Southeast Asian crops, including coconuts, giant swamp taro, and breadfruit, as well as domesticated chickens, which made the islands permanently habitable. Several Spanish expeditions visited the islands in the mid-16th century, but Spanish galleons usually sailed a Pacific route farther north and avoided the Marshalls. European maps and charts named the group for British captain John Marshall, who explored the region in 1788. American Protestant missionaries and Western business interests began arriving in the 1850s. German copra traders dominated the economy in the 1870s and 1880s, and the German Empire annexed the Marshalls as a protectorate in 1885. \n",
  Martinique:
    "It is thought that Martinique is a corruption of the Ta\u00edno name for the island (Madiana/Madinina, meaning 'island of flowers', or Matinino, 'island of women'), as relayed to Christopher Columbus when he visited the island in 1502.[8] According to historian Sydney Daney, the island was called Jouanaca\u00ebra or Iouanacaera by the Caribs, which means 'the island of iguanas'.[9]\n",
  Mauritania:
    'Mauritania is culturally and politically part of the Arab world; it is a member of the Arab League and Arabic is the official language. The official religion is Islam, and almost all inhabitants are Sunni Muslims. Despite its prevailing Arab identity, Mauritanian society is multiethnic; the Bidhan, or so-called "white moors", make up 30% of the population,[10] while the Haratin, or so-called "black moors", comprise 40%.[10] Both groups reflect a fusion of Arab-Berber ethnicity, language, and culture. The remaining 30% of the population comprises various sub-Saharan ethnic groups.\n',
  Mauritius:
    "In 1965, the UK split off the Chagos Archipelago from Mauritian territory to create the British Indian Ocean Territory (BIOT).[22] The local population was forcibly expelled and the largest island, Diego Garcia, was leased to the United States.[23] Ruling on the sovereignty dispute between Mauritius and the UK, the International Tribunal for the Law of the Sea has ordered the return of the Chagos Islands to Mauritius.[24]\n",
  Mayotte:
    "Mayotte is one of the overseas departments of France as well as one of the 18 regions of France, with the same status as the departments of Metropolitan France. It is an outermost region of the European Union and, as an overseas department of France, part of the eurozone.\n",
  Mexico:
    "Mexico is a newly industrialized and developing country,[16] with the world's 12th-largest economy by nominal GDP and 12th-largest by PPP. Mexico ranks first in the Americas and seventh in the world by the number of UNESCO World Heritage Sites.[17] It is also one of the world's 17 megadiverse countries, ranking fifth in natural biodiversity.[18] It is a major tourist destination: as of 2022, it is the sixth most-visited country in the world, with 42.2 million international arrivals.[19] Mexico's large economy and population, global cultural influence, and steady democratization make it a regional and middle power,[20][21][22] and it is increasingly identified as an emerging power.[23][24][25][26] Although Mexico has made significant political and socioeconomic gains in recent decades, it continues to struggle with social inequality, poverty, systemic corruption, and extensive crime.[27] Since 2006, an ongoing conflict between drug trafficking syndicates has led to over 120,000 deaths.[28][needs update] Mexico is a member of United Nations, the G20, the Organisation for Economic Co-operation and Development (OECD), the World Trade Organization (WTO), the Asia-Pacific Economic Cooperation forum, the Organization of American States, Community of Latin American and Caribbean States, and the Organization of Ibero-American States.\n",
  Micronesia:
    'Political control of areas within Micronesia varies depending on the island, and is distributed among six sovereign nations. Some of the Caroline Islands are part of the Republic of Palau and some are part of the Federated States of Micronesia (often shortened to "FSM" or "Micronesia"\u2014not to be confused with the identical name for the overall region). The Gilbert Islands (along with the Phoenix Islands and the Line Islands in Polynesia) comprise the Republic of Kiribati. The Mariana Islands are affiliated with the United States; some of them belong to the U.S. Territory of Guam and the rest belong to the U.S. Commonwealth of the Northern Mariana Islands. The island of Nauru is its own sovereign nation. The Marshall Islands all belong to the Republic of the Marshall Islands. The sovereignty of Wake Island is contested: it is claimed both by the United States and by the Republic of the Marshall Islands. The United States has actual possession of Wake Island, which is under the immediate administration of the United States Air Force.\n',
  Moldova:
    "On 27 August 1991, as the dissolution of the Soviet Union was underway, the Moldavian SSR declared independence and took the name Moldova.[19] However, the strip of Moldovan territory on the east bank of the Dniester has been under the de facto control of the breakaway government of Transnistria since 1990. The constitution of Moldova was adopted in 1994, and the country became a parliamentary republic with a president as head of state and a prime minister as head of government. Under the presidency of Maia Sandu, elected in 2020 on a pro-Western and anti-corruption ticket, Moldova has pursued membership of the European Union, and was granted candidate status in June 2022.[20] Accession talks to the EU began on 13 December 2023.[21] Sandu has also suggested an end to Moldova's constitutional commitment to military neutrality in favour of a closer alliance with NATO and strongly condemned Russia's invasion of neighbouring Ukraine.[22]\n",
  Monaco:
    "With an area of 2.08\u00a0km2 (0.80\u00a0sq\u00a0mi), Monaco is the second-smallest sovereign state in the world, after Vatican City. Its population of 38,367 in 2023 makes it the most densely populated sovereign state. Monaco has the world's shortest coastline: 3.83\u00a0km (2.38\u00a0mi).[16] The principality is about 15\u00a0km (9.3\u00a0mi) from the border with Italy[17] and consists of nine administrative wards, the largest of which is Monte Carlo.\n",
  Mongolia:
    "In the 16th century, Tibetan Buddhism spread to Mongolia, being further led by the Manchu-founded Qing dynasty, which absorbed the country in the 17th century. By the early 20th century, almost one-third of the adult male population were Buddhist monks.[10][11] After the collapse of the Qing dynasty in 1911, Mongolia declared independence, and achieved actual independence from the Republic of China in 1921. Shortly thereafter, the country became a satellite state of the Soviet Union. In 1924, the Mongolian People's Republic was founded as a socialist state.[12] After the anti-communist revolutions of 1989, Mongolia conducted its own peaceful democratic revolution in early 1990. This led to a multi-party system, a new constitution of 1992, and transition to a market economy.\n",
  Montenegro:
    "Before the arrival of the Slav peoples in the Balkans in the 6th and 7th centuries CE, the area now known as Montenegro was inhabited principally by people known as Illyrians.\nDuring the Early Medieval period, three principalities were located on the territory of modern-day Montenegro: Duklja, roughly corresponding to the southern half; Travunia, the west; and Rascia proper, the north.[11][12][13] The Principality of Zeta emerged in the 14th and 15th centuries. From the late 14th century to the late 18th century, large parts of southern Montenegro were ruled by the Venetian Republic and incorporated into Venetian Albania.[14] The name Montenegro was first used to refer to the country in the late 15th century. After falling under Ottoman Empire rule, Montenegro gained semi-autonomy in 1696 under the rule of the House of Petrovi\u0107-Njego\u0161, first as a theocracy and later as a secular principality. Montenegro's independence was recognised by the Great Powers at the Congress of Berlin in 1878. In 1910, the country became a kingdom. After World War I, the kingdom became part of Yugoslavia. Following the breakup of Yugoslavia, the republics of Serbia and Montenegro together proclaimed a federation. In June 2006 Montenegro declared its independence from Serbia and Montenegro following an independence referendum, creating Montenegro and Serbia as they exist today.[15] Montenegro is therefore one of the newest internationally-recognised countries in the world.[16]\n",
  Montserrat:
    "An exclusion zone was imposed, encompassing the southern part of the island as far north as parts of the Belham Valley, because of the size of the existing volcanic dome and the resulting possibility of pyroclastic activity. Visitors are generally not permitted to enter the exclusion zone, but a view of destroyed Plymouth can be seen from the top of Garibaldi Hill in Isles Bay. The volcano has been relatively quiet since early 2010 and continues to be closely monitored by the Montserrat Volcano Observatory.[11][12]\n",
  Morocco:
    "Since independence, Morocco has remained relatively stable. It has the fifth-largest economy in Africa and wields significant influence in both Africa and the Arab world; it is considered a middle power in global affairs and holds membership in the Arab League, the Arab Maghreb Union, the Union for the Mediterranean, and the African Union.[21] Morocco is a unitary semi-constitutional monarchy with an elected parliament. The executive branch is led by the King of Morocco and the prime minister, while legislative power is vested in the two chambers of parliament: the House of Representatives and the House of Councillors. Judicial power rests with the Constitutional Court, which may review the validity of laws, elections, and referendums.[22] The king holds vast executive and legislative powers, especially over the military, foreign policy and religious affairs; he can issue decrees called dahirs, which have the force of law, and can also dissolve the parliament after consulting the prime minister and the president of the constitutional court.\n",
  Mozambique:
    "Mozambique is endowed with rich and extensive natural resources, notwithstanding the country's economy is based chiefly on fishery\u2014substantially molluscs, crustaceans and echinoderms\u2014and agriculture with a growing industry of food and beverages, chemical manufacturing, aluminium and oil. The tourism sector is expanding. South Africa remains Mozambique's main trading partner, preserving a close relationship with Portugal[11] with a perspective on other European markets. Since 2001, Mozambique's GDP growth has been thriving, but since 2014/15, both a significant decrease in household real consumption and a sharp rise in economic inequality have been observed.[12] The nation remains one of the poorest and most underdeveloped countries in the world,[13] ranking low in GDP per capita, human development, measures of inequality and average life expectancy.[14]\n",
  "Myanmar (Burma)":
    "Early civilisations in the area included the Tibeto-Burman-speaking Pyu city-states in Upper Myanmar and the Mon kingdoms in Lower Myanmar.[20] In the 9th century, the Bamar people entered the upper Irrawaddy valley, and following the establishment of the Pagan Kingdom in the 1050s, the Burmese language, culture, and Theravada Buddhism slowly became dominant in the country. The Pagan Kingdom fell to Mongol invasions, and several warring states emerged. In the 16th century, reunified by the Taungoo dynasty, the country became the largest empire in the history of Southeast Asia for a short period.[21] The early 19th-century Konbaung dynasty ruled over an area that included modern Myanmar and briefly controlled Manipur and Assam as well. The British East India Company seized control of the administration of Myanmar after three Anglo-Burmese Wars in the 19th century, and the country became a British colony. After a brief Japanese occupation, Myanmar was reconquered by the Allies. On 4 January 1948, Myanmar declared independence under the terms of the Burma Independence Act 1947.\n",
  Namibia:
    "In 1884, the German Empire established rule over most of the territory, forming a colony known as German South West Africa. Between 1904 and 1908, German troops waged a punitive campaign against the Herero and Nama which escalated into the first genocide of the 20th century. German rule ended during the First World War with a 1915 defeat by South African forces. In 1920, after the end of the war, the League of Nations mandated administration of the colony to South Africa. From 1948, with the National Party elected to power, this included South Africa applying apartheid to what was then known as South West Africa. In the later 20th century, uprisings and demands for political representation resulted in the United Nations assuming direct responsibility over the territory in 1966, but South Africa maintained de facto rule until 1973, when the UN recognized the South West Africa People's Organization (SWAPO) as the official representative of the Namibian people. Namibia gained independence from South Africa on 21 March 1990, following the South African Border War. However, Walvis Bay and the Penguin Islands remained under South African control until 1994.\n",
  Nauru:
    "Settled by Micronesians circa 1000 BCE, Nauru was annexed and claimed as a colony by the German Empire in the late 19th century. After World War\u00a0I, Nauru became a League of Nations mandate administered by Australia, New Zealand, and the United Kingdom. During World War\u00a0II, Nauru was occupied by Japanese troops, and was bypassed by the Allied advance across the Pacific. After the war ended, the country entered into United Nations trusteeship. Nauru gained its independence in 1968. At various points since 2001, it has accepted aid from the Australian Government in exchange for hosting the Nauru Regional Processing Centre, a controversial offshore Australian immigration detention facility. As a result of heavy dependence on Australia, some sources have identified Nauru as a client state of Australia.[17][18][19]\n",
  Nepal:
    "The Constitution of Nepal, adopted in 2015, affirms the country as a secular federal parliamentary republic divided into seven provinces. Nepal was admitted to the United Nations in 1955, and friendship treaties were signed with India in 1950 and China in 1960. Nepal hosts the permanent secretariat of the South Asian Association for Regional Cooperation (SAARC), of which it is a founding member. Nepal is also a member of the Non-Aligned Movement and the Bay of Bengal Initiative.\n",
  Netherlands:
    'Netherlands literally means "lower countries" in reference to its low elevation and flat topography, with 26% situated below sea level.[15] Most of the areas below sea level, known as polders, are the result of land reclamation that began in the 14th century.[16] In the Republican period, which began in 1588, the Netherlands entered a unique era of political, economic, and cultural greatness, ranked among the most powerful and influential in Europe and the world; this period is known as the Dutch Golden Age.[17] During this time, its trading companies, the Dutch East India Company and the Dutch West India Company, established colonies and trading posts all over the world.[18][19]\n',
  "New Caledonia":
    "New Caledonia has a land area of 18,575\u00a0km2 (7,172\u00a0sq\u00a0mi) divided into three provinces. The North and South Provinces are on the New Caledonian mainland, while the Loyalty Islands Province is a series of four inhabited islands off the east coast of mainland (from north to south: Ouv\u00e9a, Lifou, Tiga, and Mar\u00e9). New Caledonia's population of 271,407 (October 2019 census)[10] is of diverse origins and varies by geography; in the North and Loyalty Islands Provinces, the indigenous Kanak people predominate, while the wealthy South Province contains significant populations of European (Caldoches and Metropolitan French), Kanak, and Polynesian (mostly Wallisian) origin, as well as smaller groups of Southeast Asian, Pied-Noir, and North African heritage. The capital of New Caledonia is Noum\u00e9a.[5]\n",
  "New Zealand":
    'A developed country, it was the first to introduce a minimum wage, and the first to give women the right to vote. It ranks very highly in international measures of quality of life, human rights, and it has one of the lowest levels of perceived corruption in the world. It retains visible levels of inequality, having structural disparities between its M\u0101ori and European populations. New Zealand underwent major economic changes during the 1980s, which transformed it from a protectionist to a liberalised free-trade economy. The service sector dominates the national economy, followed by the industrial sector, and agriculture; international tourism is also a significant source of revenue. New Zealand is a member of the United Nations, Commonwealth of Nations, ANZUS, UKUSA, OECD, ASEAN Plus Six, Asia-Pacific Economic Cooperation, the Pacific Community and the Pacific Islands Forum. It enjoys particularly close relations with the United States and is one of its major non-NATO allies;[15] the United Kingdom; Samoa, Fiji, and Tonga; and with Australia, with a shared "Trans-Tasman" identity between the two countries stemming from centuries of British colonisation.[16]\n',
  Nicaragua:
    "Originally inhabited by various indigenous cultures since ancient times, the region was conquered by the Spanish Empire in the 16th century. Nicaragua gained independence from Spain in 1821. The Mosquito Coast followed a different historical path, being colonized by the English in the 17th century and later coming under British rule. It became an autonomous territory of Nicaragua in 1860 and its northernmost part was transferred to Honduras in 1960. Since its independence, Nicaragua has undergone periods of political unrest, dictatorship, occupation and fiscal crisis, including the Nicaraguan Revolution of the 1960s and 1970s and the Contra War[broken anchor] of the 1980s.\n",
  Niger:
    "Its society reflects a diversity drawn from the independent histories of some ethnic groups and regions and their period living in a single state. The Hausa are the country's largest ethnic group, making up more than half the population. French is the country's official language, and ten indigenous languages have the status of national language.\n",
  Nigeria:
    "Nigeria is a multinational state inhabited by more than 250 ethnic groups speaking 500 distinct languages, all identifying with a wide variety of cultures.[12][13][14] The three largest ethnic groups are the Hausa in the north, Yoruba in the west, and Igbo in the east, together constituting over 60% of the total population.[15] The official language is English, chosen to facilitate linguistic unity at the national level.[16] Nigeria's constitution ensures de jure freedom of religion,[17] and it is home to some of the world's largest Muslim and Christian populations.[18] Nigeria is divided roughly in half between Muslims, who live mostly in the north part of the country, and Christians, who live mostly in the south; indigenous religions, such as those native to the Igbo and Yoruba ethnicities, are in the minority.[19]\n",
  Niue: "Niue's position is inside a triangle drawn between Tonga, Samoa, and the Cook Islands. It is 2,400 kilometres (1,500\u00a0mi) northeast of New Zealand, and 604 kilometres (375\u00a0mi) northeast of Tonga. Niue's land area is about 261.46 square kilometres (100.95\u00a0sq\u00a0mi)[6] and its population was 1,689 at the Census in 2022. Niue is one of the world's largest coral islands. The terrain of the island has two noticeable levels. The higher level is made up of a limestone cliff running along the coast, with a plateau in the centre of the island reaching approximately 60 metres (200\u00a0ft) above sea level. The lower level is a coastal terrace approximately 0.5\u00a0km (0.3 miles) wide and about 25\u201327 metres (80\u201390 feet) high, which slopes down and meets the sea in small cliffs. A coral reef surrounds the island, with the only major break in the reef being in the central western coast, close to the capital, Alofi. Niue is subdivided into 14 villages (municipalities). Each village has a council that elects its chairperson. The villages are at the same time electoral districts; each village sends an assemblyperson to the Niue Assembly (parliament).[15]\n",
  "Norfolk Island":
    "Native to the island, the evergreen Norfolk Island pine is a symbol of the island and is pictured on its flag. The pine is a key export for Norfolk Island, being a popular ornamental tree in Australia (where two related species grow), and also worldwide.\n",
  "North Korea":
    'North Korea is a totalitarian dictatorship with a comprehensive cult of personality around the Kim family. Amnesty International considers the country to have the worst human rights record in the world. Officially, North Korea is an "independent socialist state"[f] which holds democratic elections; however, outside observers have described the elections as unfair, uncompetitive, and pre-determined, in a manner similar to elections in the Soviet Union. The Workers\' Party of Korea is the ruling party of North Korea. According to Article 3 of the constitution, Kimilsungism\u2013Kimjongilism is the official ideology of North Korea. The means of production are owned by the state through state-run enterprises and collectivized farms. Most services\u2014such as healthcare, education, housing, and food production\u2014are subsidized or state-funded.\n',
  "North Macedonia":
    "The region's history begins with the kingdom of Paeonia. In the late sixth century BC, the area was subjugated by the Persian Achaemenid Empire, then incorporated into the Kingdom of Macedonia in the fourth century BC. The Roman Republic conquered the region in the second century BC and made it part of its larger province of Macedonia. The area remained part of the Byzantine Empire, but was often raided and settled by Slavic tribes beginning in the sixth century of the Christian era. Following centuries of contention between the Bulgarian, Byzantine, and Serbian Empires, it was part of the Ottoman Empire from the mid-14th until the early 20th century, when, following the Balkan Wars of 1912 and 1913, the modern territory of North Macedonia came under Serbian rule.\n",
  "Northern Mariana Islands":
    "The United States Department of the Interior cites a landmass of 183.5 square miles (475.26\u00a0km2).[13] According to the 2020 United States Census, 47,329 people were living in the CNMI at the time.[8] The vast majority of the population resides on Saipan, Tinian, and Rota. The other islands of the Northern Marianas are sparsely inhabited; the most notable among these is Pagan, which was left largely uninhabited since a 1981 volcanic eruption.[14]\n",
  Norway:
    "Norway has a total area of 385,207 square kilometres (148,729\u00a0sq\u00a0mi).[13] The country shares a long eastern border with Sweden, and is bordered by Finland and Russia to the northeast. Norway has an extensive coastline facing the Skagerrak strait, the North Atlantic Ocean, and the Barents Sea. Harald V of the House of Gl\u00fccksburg is the current King of Norway. Jonas Gahr St\u00f8re has been Prime Minister of Norway since 2021. As a unitary state with a constitutional monarchy, Norway divides state power between the parliament, the cabinet, and the supreme court, as determined by the 1814 constitution. The unified kingdom of Norway was established in 872 as a merger of petty kingdoms and has existed continuously for 1,151\u20131,152 years. From 1537 to 1814, Norway was part of Denmark\u2013Norway, and, from 1814 to 1905, it was in a personal union with Sweden. Norway was neutral during the First World War, and in the Second World War until April 1940 when it was invaded and occupied by Nazi Germany until the end of the war.\n",
  Oman: "Formerly a maritime empire, Oman is the oldest continuously independent state in the Arab world.[15][16] It is a member of the United Nations, the Arab League, the Gulf Cooperation Council, the Non-Aligned Movement, and the Organisation of Islamic Cooperation. It has oil reserves ranked 22nd globally.[15][17] In 2010, the United Nations Development Programme ranked Oman as the most-improved country in the world in terms of development during the preceding 40 years.[18] A portion of its economy involves tourism and trading fish, dates and other agricultural produce. The World Bank categorizes Oman as a high-income economy and as of 2023[update] Oman ranks as the 48th most peaceful country in the world according to the Global Peace Index.[19]\n",
  Pakistan:
    "Spurred by the Pakistan Movement, which sought a homeland for the Muslims of British India, and election victories in 1946 by the All-India Muslim League, Pakistan gained independence in 1947 after the Partition of the British Indian Empire, which awarded separate statehood to its Muslim-majority regions and was accompanied by an unparalleled mass migration and loss of life.[14][15] Initially a Dominion of the British Commonwealth, Pakistan officially drafted its constitution in 1956, and emerged as a declared Islamic republic. In 1971, the exclave of East Pakistan seceded as the new country of Bangladesh after a nine-month-long civil war. In the following four decades, Pakistan has been ruled by governments whose descriptions, although complex, commonly alternated between civilian and military, democratic and authoritarian, relatively secular and Islamist.[16]\n",
  Palau:
    "The country was originally settled approximately 3,000 years ago by migrants from Maritime Southeast Asia.[9][10] Palau was first drawn on a European map by the Bohemian missionary Paul Klein[11] based on a description given by a group of Palauans shipwrecked on the Philippine coast on Samar. Palau islands were made part of the Spanish East Indies in 1885. Following Spain's defeat in the Spanish\u2013American War in 1898, the islands were sold to Germany in 1899 under the terms of the German\u2013Spanish Treaty, where they were administered as part of German New Guinea. \n",
  "Palestinian Territories":
    'The Gaza Strip and the West Bank were occupied by Egypt and Jordan, respectively, since the 1948 Arab\u2013Israeli War until the Six-Day War of 1967. In 1967, Israel occupied both territories and has since maintained control. In 1980, Israel  absorbed East Jerusalem and proclaimed the entire to be its capital. The inclusion, though never formally amounting to legal annexation, was condemned internationally[21] and declared "null and void" by the United Nations General Assembly.[22][23] The Palestinian National Authority, the United Nations,[24] international legal and humanitarian bodies[25][26] and the broader international community[27][28] regard East Jerusalem as part of the West Bank, and consequently, part of the Palestinian territories. The Palestinian National Authority never exercised sovereignty over the area, although it housed its offices in Orient House and several other buildings as an assertion of its sovereign interests.[29][30] Israeli sovereignty over East Jerusalem has not been recognized by the International community on the grounds that the unilateral annexation of territory occupied during war contravenes the Fourth Geneva Convention.[31][32] The World Bank estimates the annual cost to the Palestinian economy of Israeli occupation at $3.4 billion in 2013.[33] The cost of the occupation for Israel over four decades (1967\u20132007) is estimated to be $50 billion.[34]\n',
  Panama:
    "Before the arrival of Spanish colonists in the 16th century, Panama was inhabited by a number of different indigenous tribes. It broke away from Spain in 1821 and joined the Republic of Gran Colombia, a union of Nueva Granada, Ecuador, and Venezuela. After Gran Colombia dissolved in 1831, Panama and Nueva Granada eventually became the Republic of Colombia. With the backing of the United States, Panama seceded from Colombia in 1903, allowing the construction of the Panama Canal to be completed by the United States Army Corps of Engineers between 1904 and 1914. The 1977 Torrijos\u2013Carter Treaties agreed to transfer the canal from the United States to Panama on December 31, 1999.[11] The surrounding territory was returned first, in 1979.[12]\n",
  "Papua New Guinea":
    "There are 840 known languages of Papua New Guinea (including English), making it the most linguistically diverse country in the world.[5] It is also one of the most rural countries, with only 13.25% of its population living in urban centres in 2019.[15] Most of its people live in customary communities.[16] Although government estimates reported the country's population to be 11.8 million,[17] it was reported in December 2022 that its population was in fact closer to 17 million.[18][19] Papua New Guinea is the most populous Pacific island country.\n",
  Paraguay:
    "Spanish conquistadores arrived in 1524, and in 1537 established the city of Asunci\u00f3n, the first capital of the Governorate of the R\u00edo de la Plata.[9] During the 17th\u00a0century, Paraguay was the center of Jesuit missions, where the native Guaran\u00ed people were converted to Christianity and introduced to European culture.[10] After the expulsion of the Jesuits from Spanish territories in 1767, Paraguay increasingly became a peripheral colony. Following independence from Spain in the early 19th\u00a0century, Paraguay was ruled by a series of authoritarian governments. This period ended with the disastrous Paraguayan War (1864\u20131870), during which the country lost half its prewar population and around 25\u201333% of its territory. In the 20th\u00a0century, Paraguay faced another major international conflict\u2014the Chaco War (1932\u20131935) against Bolivia\u2014in which Paraguay prevailed. The country came under a succession of military dictators, culminating in the 35-year regime of Alfredo Stroessner, which lasted until his overthrow in 1989 by an internal military coup. This marked the beginning of Paraguay's current democratic era.\n",
  Peru: "Peru formally proclaimed independence from Spain in 1821, and following the military campaigns of Bernardo O'Higgins, Jos\u00e9 de San Mart\u00edn, and Sim\u00f3n Bol\u00edvar, as well as the decisive battle of Ayacucho, it completed its independence in 1824. In the ensuing years, the country first suffered from political instability until a period of relative economic and political stability began due to the exploitation of guano that ended with the War of the Pacific (1879\u20131884). Throughout the 20th century, Peru grappled with political and social instability, including the internal conflict between the state and guerrilla groups, interspersed with periods of economic growth. Implementation of Plan Verde[13][14] shifted Peru towards neoliberal economics under the authoritarian rule of Alberto Fujimori and Vladimiro Montesinos in the 1990s, with the former's political ideology of Fujimorism leaving a lasting imprint on the country's governance that continues to present day.[15][16] The 2000s marked economic expansion and poverty reduction, but the subsequent decade revealed long-existing sociopolitical vulnerabilities, exacerbated by a political crisis instigated by Congress and the COVID-19 pandemic, precipitating the period of unrest beginning in 2022.[17]\n",
  Philippines:
    "The Philippines,[f] officially the Republic of the Philippines,[g] is an archipelagic country in Southeast Asia. In the western Pacific Ocean, it consists of 7,641 islands, with a total area of roughly 300,000 square kilometers, which are broadly categorized in three main geographical divisions from north to south: Luzon, Visayas, and Mindanao. The Philippines is bounded by the South China Sea to the west, the Philippine Sea to the east, and the Celebes Sea to the south. It shares maritime borders with Taiwan to the north, Japan to the northeast, Palau to the east and southeast, Indonesia to the south, Malaysia to the southwest, Vietnam to the west, and China to the northwest. It is the world's twelfth-most-populous country, with diverse ethnicities and cultures. Manila is the country's capital, and its most populated city is Quezon City. Both are within Metro Manila.\n",
  "Pitcairn Islands":
    "Various forms of evidence show the earliest settlers of the Pitcairn Islands were Polynesians who occupied Pitcairn and Henderson for several centuries until the islands were abandoned: Henderson most likely before the 16th century and Pitcairn in the 17th or early 18th century. The islands were uninhabited when they were discovered by Europeans.[11][12]\n",
  Poland:
    "Prehistoric human activity on Polish soil dates to the Lower Paleolithic, with continuous settlement since the end of the Last Glacial Period. Culturally diverse throughout late antiquity, in the early medieval period the region became inhabited by the West Slavic tribal Polans, who gave Poland its name. The process of establishing statehood coincided with the conversion of a pagan ruler of the Polans to Christianity, under the auspices of the Roman Catholic Church in 966. The Kingdom of Poland emerged in 1025, and in 1569 cemented its long-standing association with Lithuania, thus forming the Polish\u2013Lithuanian Commonwealth. At the time, the Commonwealth was one of the great powers of Europe, with an elective monarchy and a uniquely liberal political system, which adopted Europe's first modern constitution in 1791.\n",
  Portugal:
    "Portugal,[e] officially the Portuguese Republic,[f] is a country located on the Iberian Peninsula in Southwestern Europe, whose territory also includes the Macaronesian archipelagos of the Azores and Madeira. It features the westernmost point in continental Europe; its mainland west and south border with the North Atlantic Ocean; and in the north and east, the Portugal-Spain border, which constitutes the longest uninterrupted border line in the European Union. Portugal is the oldest nation-state in Europe. Founded in 1143, its current borders were established in mid-13th century, making them some of the most ancient in Europe and the world. Its archipelagos form two autonomous regions with their own regional governments. On the mainland, the Alentejo region occupies the biggest area but is one of the least densely populated regions of Europe. Lisbon is the capital and largest city by population, and it is also the main spot for tourists alongside Porto, the Algarve and Madeira. \n",
  "Puerto Rico":
    "Puerto Rico was settled by a succession of peoples beginning 2,000 to 4,000 years ago;[25] these included the Ortoiroid, Saladoid, and Ta\u00edno. It was claimed by Spain following the arrival of Christopher Columbus in 1493 and colonization  by Juan Ponce de Le\u00f3n in 1508.[21] Puerto Rico was contested by other European powers, but remained a Spanish possession for the next four centuries. An influx of African slaves and Spaniard settlers, primarily from the Canary Islands and Andalusia, vastly changed the cultural and demographic landscape of the island. Within the Spanish Empire, Puerto Rico played a secondary but strategic role compared to wealthier colonies like Peru and New Spain.[26][27] By the late 19th century, a distinct Puerto Rican identity began to emerge, centered around a fusion of indigenous, African, and European elements.[28][29] In 1898, following the Spanish\u2013American War, Puerto Rico was acquired by the United States.[21][30]\n",
  Qatar:
    "In early 2017, the population of Qatar was 2.6 million, although only 313,000 of them are Qatari citizens and 2.3 million being expatriates and migrant workers.[21] Its official religion is Islam.[22] The country has the fourth-highest GDP (PPP) per capita in the world[23] and the eleventh-highest GNI per capita (Atlas method).[24] It ranks 42nd in the Human Development Index, the third-highest HDI in the Arab world.[25] It is a high-income economy, backed by the world's third-largest natural gas reserves and oil reserves.[26] Qatar is one of the world's largest exporters of liquefied natural gas[27] and the world's largest emitter of carbon dioxide per capita.[28]\n",
  "R\u00e9union":
    "\nSince 1946, R\u00e9union has been governed as a French region and thus has a similar status to its counterparts in Metropolitan France. Consequently, it is one of the outermost regions of the European Union and part of the eurozone;[3] it is, along with the French overseas department of Mayotte, one of the two eurozone areas in the Southern Hemisphere. Owing to its strategic location, France maintains a large military presence.  ",
  Romania:
    "Settlement in the modern-day territory of Romania began in the Lower Paleolithic followed by written records attesting the kingdom of Dacia, its conquest, and subsequent Romanisation by the Roman Empire during late antiquity. The modern Romanian state was formed in 1859 through a personal union of the Danubian Principalities of Moldavia and Wallachia. The new state, officially named Romania since 1866, gained independence from the Ottoman Empire in 1877. During World War I, after declaring its neutrality in 1914, Romania fought together with the Allied Powers from 1916. In the aftermath of the war, Bukovina, Bessarabia, Transylvania, and parts of Banat, Cri\u0219ana, and Maramure\u0219 became part of the Kingdom of Romania.[16] In June\u2013August\u00a01940, as a consequence of the Molotov\u2013Ribbentrop Pact and Second Vienna Award, Romania was compelled to cede Bessarabia and Northern Bukovina to the Soviet Union and Northern Transylvania to Hungary. In November\u00a01940, Romania signed the Tripartite Pact and, consequently, in June\u00a01941 entered World War II on the Axis side, fighting against the Soviet Union until August\u00a01944, when it joined the Allies and recovered Northern Transylvania. Following the war and occupation by the Red Army, Romania became a socialist republic and a member of the Warsaw Pact. After the 1989\u00a0Revolution, Romania began a transition towards democracy and a market economy.\n",
  Russia:
    "In 1991, the Russian SFSR emerged from the dissolution of the Soviet Union as the Russian Federation. A new constitution was adopted, which established a federal semi-presidential system. Since the turn of the century, Russia's political system has been dominated by Vladimir Putin, under whom the country has experienced democratic backsliding and become an authoritarian dictatorship. Russia has been militarily involved in a number of conflicts in former Soviet states and other countries, including its war with Georgia in 2008 and its war with Ukraine since 2014, which has involved the internationally unrecognised annexations of Ukrainian territory including Crimea in 2014 and four other regions in 2022 during an ongoing invasion.\n",
  Rwanda:
    "Hunter-gatherers settled the territory in the Stone and Iron Ages, followed later by Bantu peoples. The population coalesced first into clans, and then into kingdoms. In the 15th century, one kingdom, under King Gihanga, managed to incorporate several of its close neighbor territories establishing the Kingdom of Rwanda. The Kingdom of Rwanda dominated from the mid-eighteenth century, with the Tutsi kings conquering others militarily, centralising power, and enacting unifying policies. In 1897, Germany colonized Rwanda as part of German East Africa, followed by Belgium, which took control in 1916 during World War I. Both European nations ruled through the Rwandan king and perpetuated a pro-Tutsi policy. The Hutu population revolted in 1959. They massacred numerous Tutsi and ultimately established an independent, Hutu-dominated republic in 1962 led by President Gr\u00e9goire Kayibanda. A 1973 military coup overthrew Kayibanda and brought Juv\u00e9nal Habyarimana to power, who retained the pro-Hutu policy. The Tutsi-led Rwandan Patriotic Front (RPF) launched a civil war in 1990. Habyarimana was assassinated in April 1994. Social tensions erupted in the Rwandan genocide that spanned one hundred days. The RPF ended the genocide with a military victory in July 1994.\n",
  Samoa:
    "Samoa was discovered and settled by the Lapita people (Austronesian people who spoke Oceanic languages), who travelled from Island Melanesia. The earliest human remains found in Samoa are dated to between roughly 2,900 and 3,500 years ago. The remains were discovered at a Lapita site at Mulifanua, and the scientists' findings were published in 1974.[17] The Samoans' origins have been studied in modern times through scientific research on Polynesian genetics, linguistics, and anthropology. Although this research is ongoing, a number of theories have been proposed. One theory is that the original Samoans were Austronesians who arrived during a final period of eastward expansion of the Lapita peoples out of Southeast Asia and Melanesia between 2,500 and 1,500 BCE.[18]\n",
  "San Marino":
    "San Marino is a landlocked country; however, its northeastern end is within ten kilometres (six miles) of the Italian city of Rimini on the Adriatic coast. The country's capital city, the City of San Marino, is located atop Monte Titano, while its largest settlement is Dogana, within the municipality of Serravalle. San Marino's official language is Italian.\n",
  "S\u00e3o Tom\u00e9 & Pr\u00edncipe":
    "The islands were uninhabited until their discovery in 1470 by Portuguese explorers Jo\u00e3o de Santar\u00e9m and Pedro Escobar. Gradually colonized and settled throughout the 16th century, they collectively served as a vital commercial and trade centre for the Atlantic slave trade. The rich volcanic soil and proximity to the equator made S\u00e3o Tom\u00e9 and Pr\u00edncipe ideal for sugar cultivation, followed later by cash crops such as coffee and cocoa; the lucrative plantation economy was heavily dependent upon enslaved Africans. Cycles of social unrest and economic instability throughout the 19th and 20th centuries culminated in peaceful independence in 1975. S\u00e3o Tom\u00e9 and Pr\u00edncipe has since remained one of Africa's most stable and democratic countries. S\u00e3o Tom\u00e9 and Pr\u00edncipe is a developing economy with a medium Human Development Index.\n",
  "Saudi Arabia":
    "The Kingdom of Saudi Arabia was founded in 1932 by King Abdulaziz (also known as Ibn Saud), who united the regions of Hejaz, Najd, parts of Eastern Arabia (Al-Ahsa) and South Arabia ('Asir) into a single state through a series of conquests, beginning in 1902 with the capture of Riyadh, the ancestral home of his family, the House of Saud. Saudi Arabia has since been an absolute monarchy governed by an authoritarian regime without public input.[23] In its Basic Law, Saudi Arabia defines itself as a sovereign Arab Islamic state with Islam as its official religion and Arabic as its official language. The ultraconservative Wahhabi religious movement within Sunni Islam was the prevailing political and cultural force in the country until the 2000s.[24][25] The Saudi government has attracted criticism for various policies such as its intervention in the Yemeni Civil War, alleged sponsorship of terrorism, and widespread human rights abuses.[26][27]\n",
  Senegal:
    "The state was formed as part of the independence of French West Africa from French colonial rule. Because of this history, French is the official language, but it is understood by only a minority of the population.[20] Over 30 languages are spoken in Senegal. Wolof is the most widely spoken one, with 80% of the population speaking it as a first or second language,[21] acting as Senegal's lingua franca alongside French. Like other African nations, the country includes a wide mix of ethnic and linguistic communities, with the largest being the Wolof, Fula, and Serer people. Senegalese people are predominantly Muslim.[22]\n",
  Serbia:
    "Serbia is an upper-middle income economy and provides universal health care and free primary and secondary education to its citizens. It is a unitary parliamentary constitutional republic, member of the UN, CoE, OSCE, PfP, BSEC, CEFTA, and is acceding to the WTO. Since 2014, the country has been negotiating its EU accession, with the possibility of joining the European Union by 2030.[13] Serbia formally adheres to the policy of military neutrality.\n",
  Seychelles:
    "Seychelles was uninhabited prior to being encountered by Europeans in the 16th century. It faced competing French and British interests until it came under full British control in the late 18th century. Since proclaiming independence from the United Kingdom in 1976, it has developed from a largely agricultural society to a market-based diversified economy, characterized by service, public sector, and tourism activities. From 1976 to 2015, nominal GDP grew nearly 700%, and purchasing power parity nearly 1600%. Since the late 2010s, the government has taken steps to encourage foreign investment.\n",
  "Sierra Leone":
    "Sierra Leone's current territorial configuration was established by the British Empire in two historical phases: in 1808, the coastal Sierra Leone Colony was founded as a place to resettle returning Africans after the abolition of the slave trade; then in 1896, the inland Protectorate was created as a result of the Berlin Conference of 1884\u20131885. This led to the formal recognition of the territory as the Sierra Leone Colony and Protectorate, or British Sierra Leone.[16][17] When Sierra Leone attained independence from the United Kingdom in 1961 under the leadership of Prime Minister Sir Milton Margai of the Sierra Leone People's Party (SLPP), the country transitioned to a Commonwealth realm and was designated the Dominion of Sierra Leone.[18]\n",
  Singapore:
    "As a highly developed country, it has one of the highest GDP per capita (PPP) in the world. It is also identified as a tax haven. Singapore is the only country in Asia with a AAA sovereign credit rating from all major rating agencies. It is a major aviation, financial, and maritime shipping hub and has consistently been ranked as one of the most expensive cities to live in for expatriates and foreign workers. Singapore ranks highly in key social indicators: education, healthcare, quality of life, personal safety, infrastructure, and housing, with a home-ownership rate of 88 percent. Singaporeans enjoy one of the longest life expectancies, fastest Internet connection speeds, lowest infant mortality rates, and lowest levels of corruption in the world. It has the third highest population density of any country in the world, although there are numerous green and recreational spaces as a result of urban planning. With a multicultural population and in recognition of the cultural identities of the major ethnic groups within the nation, Singapore has four official languages: English, Malay, Mandarin, and Tamil. English is the common language, with exclusive use in numerous public services. Multi-racialism is enshrined in the constitution and continues to shape national policies in education, housing, and politics.\n",
  "Sint Maarten":
    "Due to confusion on early maps, the island accidentally got the name Christopher Columbus had  given in honor of Saint Martin of Tours, when he first sighted Nevis on the saint's feast day on 11 November 1493.\n",
  Slovakia:
    "Slovakia (/slo\u028a\u02c8v\u00e6ki\u0259, -\u02c8v\u0251\u02d0k-/ \u24d8;[9][10] Slovak: Slovensko [\u02c8sl\u0254\u028bensk\u0254] \u24d8), officially the Slovak Republic (Slovak: Slovensk\u00e1 republika [\u02c8sl\u0254\u028benska\u02d0 \u02c8republika] \u24d8), is a landlocked country in Central Europe. It is bordered by Poland to the north, Ukraine to the east, Hungary to the south, Austria to the west, and the Czech Republic to the northwest. Slovakia's mostly mountainous territory spans about 49,000 square kilometres (19,000\u00a0sq\u00a0mi), hosting a population exceeding 5.4 million. The capital and largest city is Bratislava, while the second largest city is Ko\u0161ice.\n",
  Slovenia:
    "Slovenia's territory has been part of many different states: the Roman Empire, the Byzantine Empire, the Carolingian Empire, the Holy Roman Empire, the Kingdom of Hungary, the Republic of Venice, the Illyrian Provinces of Napoleon's First French Empire, the Austrian Empire, and the Austro-Hungarian Empire.[15] In October 1918, the Slovenes co-founded the State of Slovenes, Croats, and Serbs.[22] In December 1918, they merged with the Kingdom of Montenegro and the Kingdom of Serbia into the Kingdom of Yugoslavia.[23] During World War II, Germany, Italy, and Hungary occupied and annexed Slovenia, with a tiny area transferred to the Independent State of Croatia, a newly declared Nazi puppet state.[24] In 1945, it again became part of Yugoslavia. Post-war, Yugoslavia was allied with the Eastern Bloc, but after the Tito\u2013Stalin split of 1948, it never subscribed to the Warsaw Pact, and in 1961 it became one of the founders of the Non-Aligned Movement.[25] In June 1991, Slovenia declared independence from Yugoslavia and became an independent sovereign state.[2]\n",
  "Solomon Islands":
    "In June 1893, Captain Herbert Gibson of HMS\u00a0Curacoa declared the southern Solomon Islands a British protectorate.[13][14] During World War II, the Solomon Islands campaign (1942\u20131945) saw fierce fighting between the United States, British Imperial forces, and the Empire of Japan, including the Battle of Guadalcanal.\n",
  Somalia:
    "In 2006, with a US-backed Ethiopian intervention, the TFG assumed control of most of the nation's southern conflict zones from the newly formed Islamic Courts Union (ICU). The ICU subsequently splintered into more radical groups, including the jihadist group al-Shabaab, which battled the TFG and its AMISOM allies for control of the region.[1] By mid-2012, the insurgents had lost most of the territory they had seized, and a search for more permanent democratic institutions began.[33] Despite this, insurgents still control much of central and southern Somalia,[34][35] and wield influence in government-controlled areas,[35] with the town of Jilib acting as the de facto capital for the insurgents.[34][36] A new provisional constitution was passed in August 2012,[37][38] reforming Somalia as a federation.[39] The same month, the Federal Government of Somalia was formed[40] and a period of reconstruction began in Mogadishu, despite al-Shabaab frequently carrying out attacks there.[33][41]\n",
  "South Africa":
    "Today, about 80% of the population are Black South Africans.[6] The remaining population consists of Africa's largest communities of multiracial, European, and Asian ancestry (Coloured, White, Indian, and Chinese South Africans, respectively). South Africa is a multi-ethnic society encompassing a wide variety of cultures, languages, and religions. Its pluralistic makeup is reflected in the Constitution's recognition of 12 official languages, the fourth-highest number in the world.[16] According to the 2022 census, the two most-spoken first languages are Zulu (24.4%) and Xhosa (16.3%), which are both of the Nguni branch. The third and fifth are of European origin: Afrikaans (10.6%), developed from Dutch, serves as the first language of most Coloured and White South Africans, while English (8.7%) reflects the legacy of British colonialism and is commonly used in public and commercial life. South Africa is often referred to as the \"rainbow nation\" to describe the country's multicultural diversity, especially in the wake of apartheid.[20]\n",
  "South Georgia & South Sandwich Islands":
    'The United Kingdom claimed sovereignty over South Georgia in 1775 and the South Sandwich Islands in 1908. The territory of "South Georgia and the South Sandwich Islands" was formed in 1985;[3] previously, it had been governed as part of the Falkland Islands Dependencies. Argentina claimed South Georgia in 1927 and claimed the South Sandwich Islands in 1938.\n',
  "South Korea":
    "In 1950, a North Korean invasion began the Korean War, which ended in 1953 after extensive fighting involving the American-led United Nations Command and the People's Volunteer Army from China with Soviet assistance. The war left 3 million Koreans dead and the economy in ruins. The authoritarian First Republic of Korea led by Syngman Rhee was overthrown in the April Revolution of 1960. However, the Second Republic failed to control the revolutionary fervor. The May 16 coup of 1961 led by Park Chung Hee put an end to the Second Republic, signaling the start of the Third Republic in 1963. South Korea's devastated economy began to soar under Park's leadership, recording one of the fastest rises in average GDP per capita. Despite lacking natural resources, the nation rapidly developed to become one of the Four Asian Tigers based on international trade and economic globalization, integrating itself within the world economy with export-oriented industrialization. The Fourth Republic was established after the October Restoration of 1972, in which Park wielded absolute power. The Yushin Constitution declared that the president could suspend basic human rights and appoint a third of the parliament. Suppression of the opposition and human rights abuse by the government became more severe in this period. Even after Park's assassination in 1979, the authoritarian rule continued in the Fifth Republic led by Chun Doo-hwan, which violently seized power by two coups and brutally suppressed the Gwangju Uprising. The June Democratic Struggle of 1987 ended authoritarian rule, forming the current Sixth Republic. The country is now considered among the most advanced democracies in continental and East Asia.\n",
  "South Sudan":
    "South Sudan descended into a civil war from 2013 to 2020, enduring rampant human rights abuses, including forced displacement, ethnic massacres, and killings of journalists by various parties. It has since been governed by a coalition formed by leaders of the former warring factions, Salva Kiir Mayardit and Riek Machar.[20] The country continues to recover from the war while experiencing ongoing and systemic ethnic violence.[21]\n",
  Spain:
    "In early antiquity, the Iberian Peninsula was inhabited by Celts, Iberians, and other pre-Roman peoples. With the Roman conquest of the Iberian Peninsula, the province of Hispania was established. Following the Romanization and Christianization of Hispania, the fall of the Western Roman Empire ushered in the inward migration of tribes from Central Europe, including the Visigoths, who formed the Visigothic Kingdom centred on Toledo. In the early eighth century, most of the peninsula was invaded by the Umayyad Caliphate, and during early Islamic rule, Al-Andalus became a dominant peninsular power centred on C\u00f3rdoba. Several Christian kingdoms emerged in Northern Iberia, chief among them Asturias, Le\u00f3n, Castile, Aragon, Navarre, and Portugal; made an intermittent southward military expansion and repopulation, known as the Reconquista, repelling Islamic rule in Iberia, which culminated with the Christian seizure of the Nasrid Kingdom of Granada in 1492. The dynastic union of the Crown of Castile and the Crown of Aragon in 1479 under the Catholic Monarchs is often considered the de facto unification of Spain as a nation-state.\n",
  "Sri Lanka":
    "Sri Lanka's documented history goes back 3,000 years, with evidence of prehistoric human settlements dating back 125,000 years.[14] The earliest known Buddhist writings of Sri Lanka, known collectively as the Pali Canon, date to the fourth Buddhist council, which took place in 29 BCE.[15][16] Also called the Pearl of the Indian Ocean, or the Granary of the East, Sri Lanka's geographic location and deep harbours have made it of great strategic importance, from the earliest days of the ancient Silk Road trade route to today's so-called maritime Silk Road.[17][18][19] Because its location made it a major trading hub, it was already known to both East Asians and Europeans as long ago as the Anuradhapura period. During a period of great political crisis in the Kingdom of Kotte, the Portuguese arrived in Sri Lanka and sought to control its maritime trade, with a part of Sri Lanka subsequently becoming a Portuguese possession. After the Sinhalese-Portuguese war, the Dutch colonial empire and the Kingdom of Kandy took control of those areas. Dutch Ceylon was taken by the British Empire, which extended control over the whole island, colonising it as British Ceylon from 1815 to 1948. A national movement for political independence arose in the early 20th century, and in 1948, Ceylon became a dominion. It was succeeded by the republic of Sri Lanka in 1972. Sri Lanka's more recent history was marred by the 26-year Sri Lankan Civil War, which began in 1983 and ended in 2009, when the Sri Lanka Armed Forces defeated the Liberation Tigers of Tamil Eelam.[20]\n",
  "St. Barth\u00e9lemy":
    "Saint Barth\u00e9lemy (French: Saint-Barth\u00e9lemy, [s\u025b\u0303 ba\u0281telemi] \u24d8), officially the Collectivit\u00e9 territoriale de Saint-Barth\u00e9lemy,[7] also known as St Barts (English)[8] or St. Barth (French), is an overseas collectivity of France[9] in the Caribbean. The island lies about 30 kilometres (19\u00a0mi) southeast of the island of Saint Martin;[10] it is northeast of the Dutch islands of Saba and Sint Eustatius, as well as north of the independent country of Saint Kitts and Nevis.\n",
  "St. Helena":
    "Saint Helena measures about 16 by 8\u00a0km (10 by 5\u00a0mi) and has a population of 4,439 per the 2021 census.[2] It was named after Helena, mother of Constantine I. It is one of the most remote major islands in the world and was uninhabited when discovered by the Portuguese enroute to the Indian subcontinent in 1502. For about four centuries, the island was an important stopover for ships from Europe to Asia and back, while sailing around the African continent, until the opening of the Suez Canal. Saint Helena is the United Kingdom's second-oldest overseas territory after Bermuda.\n",
  "St. Kitts & Nevis":
    "The British dependency of Anguilla was historically also a part of this union, which was known collectively as Saint Christopher-Nevis-Anguilla. However, Anguilla chose to secede from the union in 1967, and remains a British overseas territory.[1] The islands of Sint Eustatius, Saba, Saint Barth\u00e9lemy, Saint-Martin/Sint Maarten and Anguilla lie to the north-northwest of the country. To the east and northeast are Antigua and Barbuda, and to the southeast is the small uninhabited island of Redonda (part of Antigua and Barbuda) and the island of Montserrat.\n",
  "St. Lucia":
    'The first proven inhabitants of the island, the Arawaks, are believed to have first settled in 200\u2013400 AD. Around 800 AD, the island would be taken over by the Kalinago. The French were the first Europeans to settle on the island, and they signed a treaty with the native Caribs in 1660. England took control of the island in 1663. In ensuing years, England and France fought 14 times for control of the island; consequently control over this immensely valuable geopolitical position changed frequently. Eventually, the British took complete control in 1814, shortly after the victory over French Emperor Napoleon.[12] Because the island switched so often between British and French control, Saint Lucia was also known as the "Helen of the West" after the Greek mythological character, Helen of Troy.[13]\n',
  "St. Martin": "No Information",
  "St. Pierre & Miquelon":
    "Saint-Pierre is French for Saint Peter, the patron saint of fishermen.[8]\n",
  "St. Vincent & Grenadines":
    "To the north of Saint Vincent lies Saint Lucia, to the east is Barbados, and Grenada lies to the south. Saint Vincent and the Grenadines has a population density of over 300 inhabitants/km2 (700 per sq mi), with an estimated population of 110,872.[6]\n",
  Sudan:
    "Between the 14th and 15th centuries, most of Sudan was gradually settled by Arab nomads. From the 16th to the 19th centuries, central and eastern Sudan were dominated by the Funj sultanate, while Darfur ruled the west and the Ottomans the east. In 1811, Mamluks established a state at Dunqulah as a base for their slave trading. Under Turco-Egyptian rule of Sudan after the 1820s, the practice of trading slaves was entrenched along a north\u2013south axis, with slave raids taking place in southern parts of the country and slaves being transported to Egypt and the Ottoman empire.[31]\n",
  Suriname:
    "Suriname was inhabited as early as the fourth millennium BC by various indigenous peoples, including the Arawaks, Caribs, and Wayana. Europeans arrived and contested the area in the 16th century, with the Dutch controlling much of the country's current territory by the late 17th century. Under Dutch rule, Suriname was a lucrative plantation colony focused mostly on sugar; its economy was driven by African slave labour until the abolition of slavery in 1863, after which indentured servants were recruited mostly from British India and the Dutch East Indies. In 1954, Suriname became a constituent country of the Kingdom of the Netherlands. On 25 November 1975, it became independent following negotiations with the Dutch government. Suriname continues to maintain close diplomatic, economic, and cultural ties with the Netherlands. \n",
  "Svalbard & Jan Mayen":
    "While a separate ISO code for Svalbard was proposed by the United Nations, it was the Norwegian authorities who took initiative to include Jan Mayen in the code. Its official language is Norwegian.\n",
  Sweden:
    "Sweden has been inhabited since prehistoric times, c.\u200912,000 BC. The inhabitants emerged as the Geats (Swedish: G\u00f6tar) and Swedes (Svear), which together constituted the sea-faring peoples known as the Norsemen. A unified Swedish state was established during the late 10th century. In 1397, Sweden joined Norway and Denmark to form the Scandinavian Kalmar Union,[12] which Sweden left in 1523. When Sweden became involved in the Thirty Years' War on the Protestant side, an expansion of its territories began, forming the Swedish Empire, which remained one of the great powers of Europe until the early 18th century. During this era Sweden controlled much of the Baltic Sea. Most of the conquered territories outside the Scandinavian Peninsula were lost during the 18th and 19th centuries. The eastern half of Sweden, present-day Finland, was lost to Imperial Russia in 1809. The last war in which Sweden was directly involved was in 1814, when Sweden by military means forced Norway into a personal union, a union which lasted until 1905. \n",
  Switzerland:
    "Switzerland originates from the Old Swiss Confederacy established in the Late Middle Ages, following a series of military successes against Austria and Burgundy; the Federal Charter of 1291 is considered the country's founding document. Swiss independence from the Holy Roman Empire was formally recognised in the Peace of Westphalia in 1648. Switzerland has maintained a policy of armed neutrality since the 16th century and has not fought an international war since 1815. It joined the United Nations only in 2002 but pursues an active foreign policy that includes frequent involvement in peace building.[15]\n",
  Syria:
    "The post-independence period was tumultuous, with multiple military coup attempts shaking the country between 1949 and 1971. In 1958, Syria entered a brief union with Egypt, which was terminated in the 1961 coup d'\u00e9tat and was renamed as the Arab Republic of Syria in constitutional referendum. The 1963 coup d'\u00e9tat carried out by the military committee of the Ba'ath Party established a one-party state and ran Syria under emergency law from 1963 to 2011, effectively suspending constitutional protections for citizens. Internal power-struggles within Ba'athist factions caused further coups in 1966 and 1970, which eventually resulted in the seizure of power by Hafiz al-Assad. He effectively established an Alawi minority rule to consolidate power within his family. After Assad's death, his son Bashar al-Assad inherited the presidency in 2000. Since 2011, Syria has been embroiled in a multi-sided civil war, with involvement of different countries.[c] Three political entities \u2013 the Syrian Interim Government, Syrian Salvation Government, and Rojava \u2013 have emerged in Syrian territory to challenge Assad's rule.\n",
  Taiwan:
    'In the early 1960s, Taiwan entered a period of rapid economic growth and industrialization called the "Taiwan Miracle".[40] In the late 1980s and early 1990s, the ROC transitioned from a one-party state under martial law to a multi-party democracy, with democratically elected presidents since 1996. Taiwan\'s export-oriented industrial economy is the 21st-largest in the world by nominal GDP and the 20th-largest by PPP measures, with a focus on steel, machinery, electronics, and chemicals manufacturing. Taiwan is a developed country.[41][42] It is ranked highly in terms of civil liberties,[43] healthcare,[44] and human development.[h][22]\n',
  Tajikistan:
    "The region was later conquered by the Russian Empire, before becoming part of the Soviet Union. Within the Soviet Union, the country's borders were drawn when it was part of Uzbekistan as an autonomous republic before becoming a constituent republic of the Soviet Union on 5 December 1929.[16]\n",
  Tanzania:
    "Today, the country is a presidential constitutional republic with the federal capital located in Government City, Dodoma;[16] the former capital, Dar es Salaam, retains most government offices and is the country's largest city, principal port, and leading commercial centre.[14][17][18] Tanzania is a de facto one-party state with the democratic socialist Chama Cha Mapinduzi party in power.[citation needed] The country has not experienced major internal strife since independence and is seen as one of the safest and most politically stable on the continent.[19][unreliable source?] Tanzania's population comprises about 120 ethnic,[20] linguistic, and religious groups. Christianity is the largest religion in Tanzania, with substantial Muslim and Animist minorities.[21] Over 100 languages are spoken in Tanzania, making it the most linguistically diverse country in East Africa;[22] the country does not have a de jure official language,[23][24] although the national language is Swahili.[25] English is used in foreign trade, in diplomacy, in higher courts, and as a medium of instruction in secondary and higher education,[22][26] while Arabic is spoken in Zanzibar.\n",
  Thailand:
    "Thai peoples migrated from southwestern China to mainland Southeast Asia from the 6th to 11th centuries. Indianised kingdoms such as the Mon, Khmer Empire, and Malay states ruled the region, competing with Thai states such as the Kingdoms of Ngoenyang, Sukhothai, Lan Na, and Ayutthaya, which also rivalled each other. European contact began in 1511 with a Portuguese diplomatic mission to Ayutthaya, which became a regional power by the end of the 15th century. Ayutthaya reached its peak during the 18th century, until it was destroyed in the Burmese\u2013Siamese War. King Taksin the Great quickly reunified the fragmented territory and established the short-lived Thonburi Kingdom (1767\u20131782), of which he was the only king. He was succeeded in 1782 by Phutthayotfa Chulalok (Rama I), the first monarch of the current Chakri dynasty. Throughout the era of Western imperialism in Asia, Siam remained the only state in the region to avoid colonization by foreign powers, although it was often forced to make territorial, trade, and legal concessions in unequal treaties.[11] The Siamese system of government was centralised and transformed into a modern unitary absolute monarchy during the 1868\u20131910 reign of Chulalongkorn (Rama V). In World War I, Siam sided with the Allies, a political decision made in order to amend the unequal treaties. Following a bloodless revolution in 1932, it became a constitutional monarchy and changed its official name to Thailand, becoming an ally of Japan in World War II. In the late 1950s, a military coup under Sarit Thanarat revived the monarchy's historically influential role in politics. During the Cold War, Thailand became a major ally of the United States and played an anti-communist role in the region as a member of SEATO, which was disbanded in 1977.\n",
  "Timor-Leste":
    "The national government runs on a semi-presidential system, with the popularly elected president sharing power with a prime minister appointed by the National Parliament. Power is centralised under the national government, although many local leaders have informal influence. The country maintains a policy of international cooperation, and is a member of the Community of Portuguese Language Countries, an observer of the Pacific Islands Forum, and an applicant for ASEAN membership. The country remains relatively poor, with an economy that relies heavily on natural resources, especially oil, and foreign aid.\n",
  Togo: 'Various people groups settled the boundaries of present day Togo between the 11th and 16th centuries. Between the 16th and 18th centuries, the coastal region served primarily as a European slave trading outpost, earning Togo and the surrounding region the name "The Slave Coast". In 1884, Germany declared a region including a protectorate called Togoland. After World War I, rule over Togo is transferred to France. Togo gained its independence from France in 1960.[2][19] In 1967, Gnassingb\u00e9 Eyad\u00e9ma led a successful military coup d\'\u00e9tat, after which he became president of an anti-communist, single-party state. In 1993, Eyad\u00e9ma faced multiparty elections marred by irregularities, and won the presidency three times. At the time of his death, Eyad\u00e9ma was the "longest-serving leader in modern African history", having been president for 38 years.[20] In 2005, his son Faure Gnassingb\u00e9 was elected president.\n',
  Tokelau:
    "Tokelau is officially referred to as a nation by both the New Zealand government and the Tokelauan government.[9][10][11] It is a free and democratic nation with elections every three years. However, in 2007, the United Nations General Assembly included Tokelau on its list of non-self-governing territories.[12] Its inclusion on this list is controversial, as Tokelauans have twice narrowly failed to vote for further self-determination,[note 2] and the islands' small population makes the viability of self-government challenging. The basis of Tokelau's legislative, administrative and judicial systems is the Tokelau Islands Act 1948, which has been amended several times. Since 1993, the territory has annually elected its own head of government, the Ulu-o-Tokelau. Before 1993, the administrator of Tokelau was the highest official in the government and the territory was directly administered by a New Zealand government department.\n",
  Tonga:
    "From 1900 to 1970, Tonga had British protected-state status. The United Kingdom looked after Tonga's foreign affairs under a Treaty of Friendship, but Tonga never relinquished its sovereignty to any foreign power. In 2010, Tonga took a decisive step away from its traditional absolute monarchy and became a semi-constitutional monarchy, after legislative reforms paved the way for its first partial representative elections.\n",
  "Trinidad & Tobago":
    "Unlike most Caribbean nations and territories, which rely heavily on tourism, the economy is primarily industrial with an emphasis on petroleum and petrochemicals;[17] much of the nation's wealth is derived from its large reserves of oil and natural gas.[18]\n",
  Tunisia:
    "In 2011, the Tunisian Revolution, which was triggered by dissatisfaction with the lack of freedom and democracy under the 24-year rule of President Zine El Abidine Ben Ali, overthrew his regime and catalyzed the broader Arab Spring movement across the region. Free multiparty parliamentary elections were held shortly thereafter; the country again voted for parliament on 26 October 2014,[22] and for president on 23 November 2014.[23] From 2014 to 2020, it was considered the only democratic state in the Arab world, according to the Democracy Index (The Economist).[24][c] After a democratic backsliding, Tunisia is rated a hybrid regime.[25] It is one of the few countries in Africa ranking high on the Human Development Index, with one of the highest per capita incomes on the continent, ranking 129th in GDP per capita income.\n",
  Turkey:
    "In the 19th and early 20th centuries, persecution of Muslims during the Ottoman contraction and in the Russian Empire resulted in large-scale loss of life and mass migration into modern-day Turkey from the Balkans, Caucasus, and Crimea.[28] Under the control of the Three Pashas, the Ottoman Empire entered World War I in 1914, during which the Ottoman government committed genocides against its Armenian, Greek, and Assyrian subjects.[29][30][31] Following Ottoman defeat, the Turkish War of Independence resulted in the abolition of the sultanate and the signing of the Treaty of Lausanne. The Republic was proclaimed on 29 October 1923, modelled on the reforms initiated by the country's first president, Mustafa Kemal Atat\u00fcrk. Turkey remained neutral during most of World War II,[32] but was involved in the Korean War. Coups in 1960 and 1980 interrupted the transition to a multi-party system.[33]\n",
  Turkmenistan:
    'The country is widely criticized for its poor human rights,[21][22] including for its treatment of minorities, and its lack of press and religious freedoms. Since the independence declared from the Soviet Union in 1991, Turkmenistan has been ruled by repressive totalitarian regimes: that of President for Life Saparmurat Niyazov (also known as T\u00fcrkmenba\u015fy/T\u00fcrkmenba\u015f\u0131 or "Head of the Turkmens") until his death in 2006; Gurbanguly Berdimuhamedow, who became president in 2007 after winning a non-democratic election (he had been vice-president and then acting president previously); and his son Serdar, who won a subsequent 2022 presidential election described by international observers as neither free nor fair, and now shares power with his father.[23][24][9]\n',
  "Turks & Caicos Islands":
    "The islands were inhabited for centuries by Ta\u00edno people. The first recorded European sighting of them was in 1512.[12] In subsequent centuries, they were claimed by several European powers, with the British Empire eventually gaining control. For many years they were governed indirectly through Bermuda, the Bahamas, and Jamaica. When the Bahamas gained independence in 1973, the islands received their own governor, and have remained an autonomous territory since.[7]\n",
  Tuvalu:
    "The first inhabitants of Tuvalu were Polynesians, arriving as part of the migration of Polynesians into the Pacific that began about three thousand years ago.[10] Long before European contact with the Pacific islands, Polynesians frequently voyaged by canoe between the islands. Polynesian navigation skills enabled them to make elaborately planned journeys in either double-hulled sailing canoes or outrigger canoes.[11] Scholars believe that the Polynesians spread out from Samoa and Tonga into the Tuvaluan atolls, which then served as a stepping stone for further migration into the Polynesian outliers in Melanesia and Micronesia.[12][13][14]\n",
  "U.S. Outlying Islands": "No Information",
  "U.S. Virgin Islands":
    "Previously known as the Danish West Indies of the Kingdom of Denmark\u2013Norway (from 1754 to 1814) and the independent Kingdom of Denmark (from 1814 to 1917), they were sold to the United States by Denmark for $25,000,000 in the 1917 Treaty of the Danish West Indies[8] ($595\u00a0million in 2023) and have since been an organized, unincorporated United States territory. The U.S. Virgin Islands are organized under the 1954 Revised Organic Act of the Virgin Islands and have since held five constitutional conventions. As with other territories in the United States, the Virgin Islands elects a delegate who can participate in debates in the House of Representatives but cannot vote.[11]\n",
  Uganda:
    "Yoweri Museveni's National Resistance Movement (NRM) took power in 1986 after a six-year guerrilla war. This brought stability and growth, but authoritarian practices and human rights abuses. The abolition of presidential term limits, allegations of electoral fraud and repression, have raised concerns about Uganda's democratic future. Museveni was elected president in the 2011, 2016, and 2021 general elections. Human rights issues, corruption, and regional conflicts, such as involvement in the Congo Wars and the struggle against the Lord's Resistance Army (LRA), continue to challenge Uganda. Despite this, it has made progress in education and health, improving literacy and reducing HIV infection, though challenges in maternal health and gender inequality persist. The country's future depends on addressing governance and human rights, while leveraging its natural and human resources for sustainable development.\n",
  Ukraine:
    "Ukraine gained independence in 1991 as the Soviet Union dissolved, and declared itself neutral.[11] A new constitution was adopted in 1996. A series of mass demonstrations, known as the Euromaidan, led to the establishment of a new government in 2014 after a revolution. Russia then unilaterally annexed Ukraine's Crimean Peninsula, and pro-Russian unrest culminated in a war in the Donbas between Russian-backed separatists and government forces in eastern Ukraine. Russia launched a full-scale invasion of Ukraine in 2022. Since the outbreak of war with Russia, Ukraine has continued to seek closer ties with the United States, European Union, and NATO.[12][13][14]\n",
  "United Arab Emirates":
    "The United Arab Emirates' oil and natural gas reserves are the world's seventh and seventh-largest, respectively.[20][21] Zayed bin Sultan Al Nahyan, ruler of Abu Dhabi and the country's first president, oversaw the development of the Emirates by investing oil revenues into healthcare, education, and infrastructure.[22] The country has the most diversified economy among the members of the Gulf Cooperation Council (GCC).[23] In the 21st century, the UAE has become less reliant on oil and gas and is economically focusing on tourism and business. The UAE is considered a middle power. It is also a member of the United Nations, Arab League, Organisation of Islamic Cooperation, OPEC, Non-Aligned Movement, World Trade Organization, and BRICS.  The UAE is also a dialogue partner of the Shanghai Cooperation Organisation.\n",
  "United Kingdom":
    "The UK became the first industrialised country and was the world's foremost power for the majority of the 19th and early 20th centuries, particularly during the \"Pax Britannica\" between 1815 and 1914.[26][27] At its height in the 1920s, the British Empire encompassed almost a quarter of the world's landmass and population, and was the largest empire in history. However, its involvement in the First World War and the Second World War damaged Britain's economic power and a global wave of decolonisation led to the independence of most British colonies.[28][29][30]\n",
  "United States":
    "The U.S. national government is a presidential constitutional federal republic and liberal democracy with three separate branches: legislative, executive, and judicial. It has a bicameral national legislature composed of the House of Representatives, a lower house based on population; and the Senate, an upper house based on equal representation for each state. Substantial autonomy is provided by federalism, with a political culture promoting liberty, equality, individualism, personal autonomy, and limited government.\n",
  Uruguay:
    "The area that became Uruguay was first inhabited by groups of hunter-gatherers 13,000 years ago.[12] The predominant tribe at the moment of the arrival of Europeans was the Charr\u00faa people. At the same time, there were also other tribes, such as the Guaran\u00ed and the Chan\u00e1, when the Portuguese first established Colonia do Sacramento in 1680; Uruguay was colonized by Europeans later than its neighboring countries.\n",
  Uzbekistan:
    "Uzbekistan is a secular state, with a semi-presidential constitutional government. Uzbekistan comprises 12 regions (vilayats), Tashkent City, and one autonomous republic, Karakalpakstan. While non-governmental organisations have defined Uzbekistan as \"an authoritarian state with limited civil rights\",[17][2] significant reforms under Uzbekistan's second president, Shavkat Mirziyoyev, have been made following the death of the first president, Islam Karimov. Owing to these reforms, relations with the neighbouring countries of Kyrgyzstan, Tajikistan, and Afghanistan have drastically improved.[18][19][20][21] A United Nations report of 2020 found much progress toward achieving the UN's Sustainable Development Goals.[22]\n",
  Vanuatu:
    "In the 1880s, France and the United Kingdom claimed parts of the archipelago, and in 1906, they agreed on a framework for jointly managing the archipelago as the New Hebrides through an Anglo-French condominium.\n",
  "Vatican City":
    "With an area of 49 hectares (121 acres)[c] and as of 2023 a population of about 764,[13] it is the smallest state in the world both by area and by population.[21] It is also the second-least populated capital in the world. As governed by the Holy See, Vatican City State is an ecclesiastical or sacerdotal-monarchical state ruled by the Pope, who is the bishop of Rome and head of the Catholic Church.[6][22] The highest state functionaries are all Catholic clergy of various origins. After the Avignon Papacy (1309\u20131377) the popes have mainly resided at the Apostolic Palace within what is now Vatican City, although at times residing instead in the Quirinal Palace in Rome or elsewhere.\n",
  Venezuela:
    "Economic shocks in the 1980s and 1990s led to major political crises and widespread social unrest, including the deadly Caracazo riots of 1989, two attempted coups in 1992, and the impeachment of a President for embezzlement of public funds charges in 1993. The collapse in confidence in the existing parties saw the 1998 Venezuelan presidential election, the catalyst for the Bolivarian Revolution, which began with a 1999 Constituent Assembly, where a new Constitution of Venezuela was imposed. The government's populist social welfare policies were bolstered by soaring oil prices,[24] temporarily increasing social spending,[25] and reducing economic inequality and poverty in the early years of the regime.[26] However, poverty began to rapidly increase in the 2010s.[27][28] The 2013 Venezuelan presidential election was widely disputed leading to widespread protest, which triggered another nationwide crisis that continues to this day.[29] Venezuela has experienced democratic backsliding, shifting into an authoritarian state.[3] It ranks low in international measurements of freedom of the press and civil liberties and has high levels of perceived corruption.[30] Venezuela is a developing country having the world's largest known oil reserves and has been one of the world's leading exporters of oil. Previously, the country was an underdeveloped exporter of agricultural commodities such as coffee and cocoa, but oil quickly came to dominate exports and government revenues. The excesses and poor policies of the incumbent government led to the collapse of Venezuela's entire economy.[31][32] The country struggles with record hyperinflation,[33][34] shortages of basic goods,[35] unemployment,[36] poverty,[37] disease, high child mortality, malnutrition, environmental issues, severe crime and corruption. These factors have precipitated the Venezuelan refugee crisis in which more than 7.7 million people had fled the country by June 2024.[38][39] By 2017, Venezuela was declared to be in default regarding debt payments by credit rating agencies.[40][41] The crisis in Venezuela has contributed to a rapidly deteriorating human rights situation.\n",
  Vietnam:
    "Vietnam was inhabited by the Paleolithic age, with states established in the first millennium BC on the Red River Delta in modern-day northern Vietnam. The Han dynasty annexed Northern and Central Vietnam under Chinese rule from 111 BC, until the first dynasty emerged in 939. Successive monarchical dynasties absorbed Chinese influences through Confucianism and Buddhism, and expanded southward to the Mekong Delta, conquering Champa. During most of the 17th and 18th centuries, Vietnam was effectively divided into two domains of \u0110\u00e0ng Trong and \u0110\u00e0ng Ngo\u00e0i. The Nguy\u1ec5n\u2014the last imperial dynasty\u2014surrendered to France in 1883. In 1887, its territory was integrated into French Indochina as three separate regions. In the immediate aftermath of World War II, the nationalist coalition Viet Minh, led by the communist revolutionary Ho Chi Minh, launched the August Revolution and declared Vietnam's independence from the Empire of Japan in 1945.\n",
  "Wallis & Futuna":
    "Since 28 March 2003, Wallis and Futuna has been a French overseas collectivity (collectivit\u00e9 d'outre-mer, or COM).[4] Between 1961 and 2003, it had the status of a French overseas territory (territoire d'outre-mer, or TOM). Its official name did not change with the change in its status.\n",
  "Western Sahara":
    "Since a UN-sponsored ceasefire agreement in 1991, most of the territory has been administered by the Moroccan government, with tacit support from France and the United States. The remainder is administered by the SADR, backed by Algeria.[16] The only part of the coast in SADR territory is the extreme south. Internationally, countries such as Russia have taken an ambiguous and neutral position on each side's claims and pressed parties to agree on a peaceful resolution. Morocco and Polisario have sought to boost their claims by accumulating formal recognition, especially from African, Asian, and Latin American states in the developing world. The Polisario Front has won formal recognition for the SADR from 46 UN member states and South Ossetia, and was extended membership of the African Union. Morocco has won support from several African governments and most of the Muslim world and Arab League.[17][unreliable source?] In most instances, recognitions are extended or withdrawn due to a change in relations with Morocco. Until 2020, no other member state of the UN had ever recognized Moroccan sovereignty over parts of Western Sahara.[18][19][20] In 2020, the US recognized Moroccan sovereignty over Western Sahara in exchange for Moroccan normalization of relations with Israel.[21][22] In 2023, Israel recognized Moroccan sovereignty.[23]\n",
  Yemen:
    "Since 2011, Yemen has been enduring a political crisis, marked by street protests against poverty, unemployment, corruption, and President Saleh's plan to amend Yemen's constitution and eliminate the presidential term limit.[23] By 2015, the country became engulfed by an ongoing civil war with multiple entities vying for governance, including the Presidential Leadership Council of the internationally recognized government, and the Houthi movement's Supreme Political Council. This conflict, which has escalated to involve various foreign powers, has led to a severe humanitarian crisis.[24][25][26][27][28][29]\n",
  Zambia:
    'On 24 October 1964, Zambia became independent of the United Kingdom and prime minister Kenneth Kaunda became the inaugural president. Kaunda\'s socialist United National Independence Party (UNIP) maintained power from 1964 until 1991 with him playing a key role in regional diplomacy, cooperating closely with the United States in search of solutions to conflicts in Southern Rhodesia (Zimbabwe), Angola, and Namibia.[10] From 1972 to 1991, Zambia was a one-party state with UNIP as the sole legal political party under the motto "One Zambia, One Nation" coined by Kaunda. Kaunda was succeeded by Frederick Chiluba of the social-democratic Movement for Multi-Party Democracy in 1991, beginning a period of socio-economic development and government decentralisation. Zambia has since become a multi-party state and has experienced several peaceful transitions of power.\n',
  Zimbabwe:
    "A country of roughly 15 million people as per 2022 census,[15] Zimbabwe's largest ethnic group are the Shona, who make up 80% of the population, followed by the Northern Ndebele and other smaller minorities. Zimbabwe has 16 official languages,[3] with English, Shona, and Ndebele the most common. Zimbabwe is a member of the United Nations, the Southern African Development Community, the African Union, and the Common Market for Eastern and Southern Africa.\n",
};
